import { Container, Row, Col, Tab } from "react-bootstrap";
import React, { useEffect, useState } from "react";
import styles from "student/App.module.css";
import { Switch, Route, Redirect, withRouter } from "react-router-dom";
import Sidebar from "student/components/Sidebar/Sidebar";
import NavbarBoot from "student/components/Navbar/Navbar";
import { useDispatch, useSelector } from "react-redux";
import { refreshApi } from "student/http";
import { setLogIn } from "student/store/auth";
import { getAdimnApi } from "student/http";
import { setReceiver } from "student/store/order";
import DLayout from "student/layout/DLayout";
import { refreshAdminApi } from "student/http";
import { setAdminLogIn } from "student/store/auth";
import { toast } from "react-toastify";
import { setOrder } from "student/store/order";
import { useHistory } from "react-router-dom";
import { addOByM } from "student/store/order";
import { setOByM } from "student/store/order";
import { getUserChatOrderApi } from "student/http";
import { getChatOrderApi } from "student/http";
import { setLogOut } from "student/store/auth";
import { addNewOrder } from "student/store/order";
import { addNotification } from "student/store/notification";
import { setNotification } from "student/store/notification";
import { getNotificationApi } from "student/http";
import {socket} from 'student/http'




const Layout = ({ routers = [], match }) => {
  const { user } = useSelector((s) => s.auth);
  const { order } = useSelector((s) => s.order);
  const dispatch = useDispatch();
  const history = useHistory();
  const [isUnmount, setIsUnmount] = useState(false);
  const [tempMessage, setTempMessage] = useState(null);
  const [tempNotification, setTempNotification] = useState(null);
  const localUser =  JSON.parse(localStorage.getItem('user'));
  // console.log('localUser',localUser);
  console.log('Order', order);

 
  useEffect(() => {
    if (user._id) return;
    (async () => {
      try {
        if (!!localStorage.getItem("studentToken")) {
          await refreshApi()
            .then((res) => {
              if (!isUnmount) {
                dispatch(setLogIn(res.data));
              }
            })
            .catch((err) => {
              console.log(err.response.data);
              dispatch(setLogOut());
            });
        } else {
          await refreshAdminApi()
            .then((res) => {
              if (!isUnmount) {
                dispatch(setAdminLogIn(res.data));
              }
            })
            .catch((err) => {
              console.log(err.response.data);
              dispatch(setLogOut());
            });
        }
      } catch (err) {
        console.log(err);
      }
    })();
    return () => {
      setIsUnmount(true);
      socket.emit("forceDisconnect",user._id);
    };
  }, []);

  useEffect(() => {

    socket.on("connect", () => {
      console.log('Socket Connectes',socket.id); // x8WIv7-mJelg7on_ALbx
      socket.emit("joinChat", { sender: localUser, _id: localUser._id });
    });
    socket.on("orderMessage", (data) => {
      console.log('socketOrderMessage',data);
      return setTempMessage(data);
    });
    socket.on("notification", (data) => {
      setTempNotification(data);
    });
    socket.on("disconnect", (reason) => {
      console.log("Socket Disconnected");
      if (reason === "io server disconnect") {
        // the disconnection was initiated by the server, you need to reconnect manually
        socket.connect();
      }
    });

    socket.on("connect_error", (err) => {
      console.log(`connect_error due to ${err.message}`);
    });
  }, [socket]);

  useEffect(() => {
    if (user._id) {
      (async () => {
        try {
          const { data } = await getNotificationApi(user._id);
          if (data?.data) {
            if (!isUnmount) {
              dispatch(setNotification(data.data));
            }
          }
        } catch (e) {
          console.log(e);
        }
      })();
      (async () => {
        const api =
          user.role === "user"
            ? getUserChatOrderApi(user._id)
            : getChatOrderApi(user._id);
        try {
          const { data } = await api;
          if (data?.data) {
            if (!isUnmount) {
              dispatch(setOByM(data.data));
            }
          }
        } catch (e) {
          console.log(e);
        }
      })();
      if (user.role === "user") {
        (async () => {
          try {
            const { data } = await getAdimnApi();
            if (data) {
              if (!isUnmount) {
                dispatch(setReceiver(data.data));
              }
            }
          } catch (e) {
            console.log(e);
          }
        })();
      }
    }
    return () => {
      setIsUnmount(true);
    };
  }, [user]);

  useEffect(() => {
    if (tempMessage) {
      dispatch(addOByM(tempMessage));
    }
  }, [JSON.stringify(tempMessage)]);

  useEffect(() => {
    if (tempNotification) {
      ((data) => {
        toast.success(
          <div onClick={() => toastClickHandler(data.info)}>
            {data.notification} <br />
            type: {data.type}
          </div>,
          {
            autoClose: 5000,
            pauseOnHover: true,
            draggable: true,
          }
        );
        dispatch(addNewOrder(data.info));
        dispatch(addNotification(data));
      })(tempNotification);
    }
  }, [JSON.stringify(tempNotification)]);

  const toastClickHandler = (v) => {
    dispatch(setOrder(v));
    dispatch(setReceiver(v.userId));
    history.push("/admin/dashboard/inner-order");
  };

  return (
    <div className={styles.mainBody}>
      <div className={styles.main}>
        <NavbarBoot />
        <section>
          <Container className="pt-5">
            <Row className="position-relative mt-5">
              <Col md={3}>
                <Sidebar routers={routers} />
              </Col>
              <Col md={9} className="pr-5 position-relative pb-5 pb-md-0">
                <Switch>
                  {routers.map((e, i) => {
                    return (
                      <Route
                        key={i}
                        path={`${match.path}${e.path}`}
                        render={() => {
                          return (
                            <Switch>
                              {e.children
                                ? e.children.map((c, j) => (
                                    <Route
                                      key={j}
                                      path={`${match.path}${e.path}${c.path}`}
                                      children={
                                        <DLayout title={c.name}>
                                          {c.component}
                                        </DLayout>
                                      }
                                    />
                                  ))
                                : ""}
                              <Route
                                path={`${match.path}${e.path}`}
                                component={e.component}
                              />
                            </Switch>
                          );
                        }}
                      />
                    );
                  })}
                  <Redirect to={`${match.path}/dashbord`} />
                </Switch>
              </Col>
            </Row>
          </Container>
        </section>
      </div>
    </div>
  );
};

export default withRouter(Layout);
