import react, {useState, useEffect} from 'react';
import styles from './LoadingBadge.module.css';
import { Spinner, Badge } from 'react-bootstrap';

const LoadingBadge = ({ children }) => {
  const [loading, setLoding] = useState(false);
  useEffect(() => {
    setLoding(true);
    const timeout = setTimeout(() => setLoding(false), 3000);
    return () => {
      clearTimeout(timeout);
    };
  }, [children]);
  return (
    <>
      {loading && (
        <Spinner
          animation="border"
          className={styles.spinner}
          variant="warning"
        ></Spinner>
      )}
      <Badge className={styles.badge} pill bg="danger">
        {children}
      </Badge>
    </>
  );
};

export default LoadingBadge;
