import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { setSeenOrderApi } from "student/http";


const orderSlice = createSlice({
  name: "order",
  initialState: {
    order: {
      _id: "",
      name: "",
      email: "",
      paperType: "",
      paperLength: "",
      education: "",
      referencing: "",
      message: "",
      question: "",
      deadline: "",
      country: "",
      files: [],
    },
    receiver: null,
    totalOrders: {
      data: [],
      total: 0,
    },
    newOrders: [],
    orderByMessage: [],
  },
  reducers: {
    setOrder: (state, action) => {
      state.order = action.payload;
      
      if(action.payload._id === '')return
      
      const index = state.orderByMessage.findIndex(
        (e) => e._id === action.payload?._id
      );
      if (index === -1) {
        state.orderByMessage.unshift({
          ...action.payload,
          unseenMessages: [],
        });
      }else{
        if(state.orderByMessage[index].unseenMessages.length > 0){
          state.orderByMessage[index].unseenMessages = [];
          setSeenOrderApi(action.payload._id);
        } 
      }
    },
    setTotalOrders: (state, action) => {
      state.totalOrders.data = action.payload.data;
      state.totalOrders.total = action.payload.total;

      // console.log('sotre',store.getState().auth);
    },
    setReceiver: (state, action) => {
      state.receiver = action.payload;
      if (typeof action.payload._id === "object") {
        state.receiver = action.payload._id;
      }
    },
    setNewOrder: (state, action) => {
      state.newOrders = action.payload;
    },
    addNewOrder: (state, action) => {
      state.newOrders.unshift(action.payload);
    },
    setOByM: (state, action) => {
      state.orderByMessage = action.payload;
    },
    addOByM: (state, action) => {
      if (action.payload.order._id === state.order._id) {
        try {
          const { data } = setSeenOrderApi(action.payload._id);
          // console.log(data);
        } catch (E) {
          console.log(E);
        }
        return;
      }
      const temp = action.payload;
      let load = state.orderByMessage;
      const newTemp = {
        _id: Math.random(),
        message: temp?.text,
        file: temp?.file,
        orderId: temp?.orderId,
        sender: temp?.sender?._id,
      };
      const index = load.findIndex((e) => e._id === temp.order._id);
      if (index === -1) {
        load.unshift({ ...temp.order, unseenMessages: [newTemp] });
      } else {
        const td = load[index].unseenMessages;
        load = load.filter((e, i) => i !== index);
        load.unshift({ ...temp.order, unseenMessages: [newTemp, ...td] });
      }
      state.orderByMessage = load;
    },
    rmOByM: (state, action) => {
      const load = action.payload;
      const index = state.orderByMessage.findIndex((e) => e._id === load?._id);
      state.order = load;
      state.orderByMessage[index].unseenMessages = [];
      try {
        const { data } = setSeenOrderApi(action.payload._id);
        // console.log(data);
      } catch (E) {
        console.log(E);
      }
      // const states = state.orderByMessage;
    },
  },
});

export const {
  setOrder,
  setTotalOrders,
  setReceiver,
  setNewOrder,
  addNewOrder,
  setOByM,
  addOByM,
  rmOByM,
} = orderSlice.actions;

export default orderSlice.reducer;
