import React, { useEffect, useState } from "react";
import {
  Table,
  Pagination,
  Row,
  Col,
  OverlayTrigger,
  Tooltip,
  Button,
} from "react-bootstrap";
import { FaRegEye, FaRegComments } from "react-icons/fa";
import { Link, useHistory } from "react-router-dom";
import { IconContext } from "react-icons";
import { useDispatch } from "react-redux";
import { setOrder, setReceiver } from "student/store/order";
import { getUserTranslationOrderApi } from "student/http";
import { useSelector } from "react-redux";
import moment from "moment";

import styles from "./UserOrderTable.module.css";
import { setOrderOfUserTable } from "student/store/table";

const TableBoot = () => {
  const dispatch = useDispatch();
  const history = useHistory();

  const { receiver } = useSelector((s) => s.order);
  const { data: tableData, total: totalData } = useSelector(
    (s) => s.table.orderOfUserTable
  );
  const { user } = useSelector((s) => s.auth);
  // const [tableData, setTableData] = useState([]);
  // const [totalData, setTotalData] = useState(5);
  const [pagData, setPagData] = useState({
    page: 0,
    row: 5,
  });

  // console.log("DATA", tableData);

  React.useEffect(() => {
    if (user._id) {
      (async () => {
        try {
          const { data } = await getUserTranslationOrderApi(
            receiver._id,
            pagData
          );
          if (data) {
            // console.log('apiData',data)
            if (data.data) {
              dispatch(setOrderOfUserTable(data));
            }
          }
        } catch (e) {
          console.log(e);
        }
      })();
    }
    return () => {
      // setTableData([]);
    };
  }, [user, pagData]);
  const totalPages = Math.ceil(+totalData / +pagData.row);
  const pagNumber = [...Array(totalPages || 10).keys()] || [];

  const pagHandler = (a) => {
    if (a === "next") {
      if (pagData.page !== pagNumber.length - 1)
        setPagData({ ...pagData, page: +pagData.page + 1 });
    } else {
      if (pagData.page >= 1)
        setPagData({ ...pagData, page: +pagData.page - 1 });
    }
  };

  const viewClickHandler = (v) => {
    dispatch(setOrder(v));
    if (user.role !== "user") {
      dispatch(setReceiver(v.userId));
    }
    const url =
      user.role === "admin"
        ? "/admin/dashboard/inner-order"
        : "/dashbord/inner-order";
    history.push(url);
  };
  const messageClickHandler = (v) => {
    dispatch(setOrder(v));
    if (user.role !== "user") {
      dispatch(setReceiver(v.userId));
    }
    const url =
      user.role === "admin"
        ? "/admin/dashboard/chat-support"
        : "/dashbord/chat-support";
    history.push(url);
  };
  const setPagDataHandler = (v) => {
    setPagData({ ...pagData, page: v });
  };

  return (
    <div className="bg-white rounded overflow-scroll">
      <IconContext.Provider
        value={{ color: "#000", size: 20, paddingLeft: 10 }}
      >
        <Table striped className={styles.table}>
          <thead className={styles.tableInfo}>
            <tr className={styles.tableInfo}>
              <th>Translation Id</th>
              <th>Deadline</th>
              <th>country</th>
              <th>Source Language</th>
              <th>Service Required</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody
            className={`${styles.tableInfo} ${styles.table} ${styles.tableBody} `}
          >
            {tableData.length ? (
              tableData.map((e) => {
                return (
                  <tr key={e._id} className={styles.tableInfo}>
                    <td>
                      <OverlayTrigger
                        placement="top"
                        overlay={
                          <Tooltip id={`tooltip-top`}>Order Details</Tooltip>
                        }
                      >
                        <Button
                          variant="link"
                          className="text-decoration-none text-dark fw-bold"
                          onClick={() => viewClickHandler(e)}
                        >
                          {e.translationId}
                        </Button>
                      </OverlayTrigger>
                    </td>
                    <td>{moment(e.deadline).format("DD/MM/YYYY")}</td>
                    <td>{e.country}</td>
                    <td>{e.sourceLanguage}</td>
                    <td>{e.service_req}</td>
                    <td>
                      <div className="d-flex m-0 p-0">
                        <OverlayTrigger
                          placement="top"
                          overlay={<Tooltip id={`tooltip-top`}>view</Tooltip>}
                        >
                          <Button
                            className="p-1"
                            variant="link"
                            onClick={() => viewClickHandler(e)}
                          >
                            <FaRegEye />
                          </Button>
                        </OverlayTrigger>
                        <OverlayTrigger
                          placement="top"
                          overlay={
                            <Tooltip id={`tooltip-top`}>messages</Tooltip>
                          }
                        >
                          <Button
                            variant="link"
                            className="p-1"
                            onClick={() => messageClickHandler(e)}
                          >
                            <FaRegComments />
                          </Button>
                        </OverlayTrigger>
                      </div>
                    </td>
                  </tr>
                );
              })
            ) : (
              <tr className={styles.tableInfo}>
                <td colSpan="5" className="text-center bg-white">
                  There is no Any Order{" "}
                </td>
              </tr>
            )}
          </tbody>
        </Table>
      </IconContext.Provider>

      <div className="p-1 m-2 d-flex flex-row justify-content-between">
        <p className="">
          Showing {+pagData.page * +pagData.row} to{" "}
          {(+pagData.page + 1) * pagData.row} of {totalData} entries
        </p>
        <Pagination className="">
          <Pagination.Item onClick={() => pagHandler("pre")}>
            {"Privious"}
          </Pagination.Item>
          {+pagData.page > 2 && (
            <>
              <Pagination.Item onClick={() => setPagDataHandler(0)}>
                {1}
              </Pagination.Item>
              <Pagination.Ellipsis />
            </>
          )}

          {pagData.page > 1 && (
            <Pagination.Item
              onClick={() => setPagDataHandler(+pagData.page - 2)}
            >
              {+pagData.page - 1}
            </Pagination.Item>
          )}
          {pagData.page > 0 && (
            <Pagination.Item
              onClick={() => setPagDataHandler(+pagData.page - 1)}
            >
              {+pagData.page}
            </Pagination.Item>
          )}

          <Pagination.Item
            // onClick={() => setPagData({ ...pagData, page: +e })}
            active
          >
            {+pagData.page + 1}
          </Pagination.Item>
          {+pagData.page <= totalPages - 2 && (
            <Pagination.Item
              onClick={() => setPagDataHandler(+pagData.page + 1)}
            >
              {+pagData.page + 2}
            </Pagination.Item>
          )}

          {+pagData.page <= totalPages - 3 && (
            <Pagination.Item
              onClick={() => setPagDataHandler(+pagData.page + 2)}
            >
              {+pagData.page + 3}
            </Pagination.Item>
          )}

          {+pagData.page <= totalPages - 4 && (
            <>
              <Pagination.Ellipsis />
              <Pagination.Item
                onClick={() => setPagDataHandler(+totalPages - 1)}
              >
                {+totalPages}
              </Pagination.Item>
            </>
          )}

          <Pagination.Item onClick={() => pagHandler("next")}>
            {"Next"}
          </Pagination.Item>
        </Pagination>
      </div>
    </div>
  );
};

export default TableBoot;
