import React, { useState, useEffect } from "react";
import styles from "./InnerUser.module.css";
import { Row, Col } from "react-bootstrap";
import Chat from "student/components/Chat/Chat";
import { useSelector } from "react-redux";
import moment from "moment";
import { Redirect } from "react-router-dom";
import UserOrderTable from 'student/components/Table/UserOrderTable/UserOrderTable'
import ImageCustom from "student/components/ImageCustom/ImageCustom";

const InnerUser = () => {
  const { receiver } = useSelector((s) => s.order);
  // console.log('receiver',receiver)
  if (!receiver) return <Redirect to="/" />;
  return (
    <div className={styles.bgLightWhite}>
      <div className={`${styles.bgSky} rounded-top`}>
        <div className="p-2 text-center">
          <h5>
            Name:
            <span className="text-dard fw-bold"> {receiver?.name}</span>
          </h5>
        </div>
        <div className="p-2 bg-white rounded-bottom">
          <div className="p-2">
            <Row>
              <Col md={2} className="p-0">
                <div className={` ${styles.bgSky} text-center rounded`}>
                  <ImageCustom
                    textStyle={{ fontSize: 80 }}
                    className={`${styles.profileImg} img-fluid`}
                    src={receiver?.image}
                    alt={receiver?.name}
                  />
                </div>
              </Col>
              <Col md={10}>
                <Row>
                  <Col md={4} className="p-0">
                    <div
                      className={`${styles.orderText} ${styles.bgSky} rounded`}
                    >
                      <p className={styles.filedParagraph}>
                        Name :
                        <span className="text-dark">
                          {" "}
                          {` ${receiver?.name}`}
                        </span>
                      </p>
                    </div>
                  </Col>
                  <Col md={4} className="p-0">
                    <div
                      className={`${styles.orderText} ${styles.bgSky} rounded`}
                    >
                      <p className={styles.filedParagraph}>
                        Email :
                        <span className="text-dark">
                          {` ${receiver?.email}`}
                        </span>
                      </p>
                    </div>
                  </Col>
                  <Col md={4} className="p-0">
                    <div
                      className={`${styles.orderText} ${styles.bgSky} rounded`}
                    >
                      <p className={styles.filedParagraph}>
                        Phone :
                        <span className="text-dark">{` ${receiver?.phone}`}</span>
                      </p>
                    </div>
                  </Col>
                  <Col md={4} className="p-0">
                    <div
                      className={`${styles.orderText} ${styles.bgSky} rounded`}
                    >
                      <p className={styles.filedParagraph}>
                        Since :
                        <span className="text-dark">
                          {` ${
                            receiver?.createdAt
                              ? moment(receiver?.createdAt).format("DD/MM/YYYY")
                              : ""
                          }`}
                        </span>
                      </p>
                    </div>
                  </Col>
                  <Col md={4} className="p-0">
                    <div
                      className={`${styles.orderText} ${styles.bgSky} rounded`}
                    >
                      <p className={styles.filedParagraph}>
                        Country :
                        <span className="text-dark">{` ${receiver?.country}`}</span>
                      </p>
                    </div>
                  </Col>
                  <Col md={4} className="p-0">
                    <div
                      className={`${styles.orderText} ${styles.bgSky} rounded`}
                    >
                      <p className={styles.filedParagraph}>
                        Orders :
                        <span className="text-dark">{` ${receiver?.orders}`}</span>
                      </p>
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>
          </div>
        </div>
      </div>

      <div className="mt-3">
        <div className="pr-5">
          <UserOrderTable />
        </div>
      </div>
    </div>
  );
};

export default InnerUser;
