import React, {useState}from 'react'
const ImageCustom = ({ src, alt, textStyle, ...rest }) => {
  const [load, setLoad] = useState(false);
  return (
    <div>
      {!load ? (
        <div style={{ backgroundColor: "rgb(195 192 192)" }} {...rest}>
          <h4
            style={{
              color: "#fff",
              textAlign: "center",
              ...textStyle,
            }}
          >{`${alt?.charAt(0) || `I`}`}</h4>
        </div>
      ) : (
        ""
      )}
      <img
        {...rest}
        style={load ? {} : { display: "none" }}
        src={src}
        alt={alt}
        onLoad={() => setLoad(true)}
      />
    </div>
  );
};
export default ImageCustom;