import React,{useState} from 'react'
import styles from './Forgot.module.css'
import { Container, Row, Col, Form,Button } from 'react-bootstrap'
import { Link, useHistory } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { forgotApi } from 'student/http'
import { setForgot } from 'student/store/auth'

const Forgot = () => {
    const dispatch = useDispatch();
    const history = useHistory();
    const [email, setEmail] = useState('');
    const onSubmitHandler = async() =>{
        if(!email)return
        try {
            const {data} = await forgotApi({email});
            if(data){
                dispatch(setForgot(data));
                history.push("/reset");
            } 
        } catch (er) {
            console.log(er);   
        }
    }
    return (
      <div className={`${styles.forgotBody} p-2 min-vh-100 `}>
        <div className={styles.bgImg}>
          <Container>
            <Row className="">
              <Col md={1}></Col>
              <Col className={styles.whiteBg}>
                <Row className="m-0 h-100">
                  <Col md={6} className="position-relative d-none d-sm-block">
                    <img
                      className="img-fluid w-100"
                      src="/images/Forgot.png"
                      alt="forgot"
                    />
                  </Col>
                  <Col md="6" className="p-0">
                    <div className={`${styles.orengeBg} h-100`}>
                      <div>
                        <h2 className={styles.heading}>Forgot Password</h2>
                      </div>
                      <div className="continue">
                        <h4>New Password</h4>
                        <div className={styles.lineWhite}></div>
                      </div>
                      <div className={styles.formSection}>
                        <Form.Group
                          className="mb-3 text-white"
                          controlId="exampleForm.ControlInput1"
                        >
                          <Form.Label className="text-white">
                            Email address
                          </Form.Label>
                          <Form.Control
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            type="email"
                            placeholder="name@example.com"
                          />
                        </Form.Group>
                      </div>
                      <div className="text-center">
                        <Button
                          onClick={onSubmitHandler}
                          className={`${styles.btnPrimary} px-5`}
                        >
                          submit
                        </Button>
                      </div>
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    );
}

export default Forgot
