import React, { useState } from "react";
import {
  Col,
  Row,
  Button,
  Collapse,
  Card,
  Form,
  Tab,
  Nav,
  Container,
} from "react-bootstrap";

import styles from "./DLayout.module.css";
import Search from "student/components/Search/Search";


const DLayout = ({ children, title }) => (
  <div className={`${styles.bgLightWhite} position-relative`}>
    <Tab.Container id="left-tabs-dashboard-example">
      <div className={`${styles.darkBlue} rounded-top p-2`}>
        <Search text={title} />
      </div>
      <div className="tab-content mt-4">
        <Tab.Content>{children}</Tab.Content>
      </div>
    </Tab.Container>
  </div>
);

export default DLayout
