import React, { useEffect, useState } from "react";
import {
  Table,
  Pagination,
  Row,
  Col,
  OverlayTrigger,
  Tooltip,
  Button,
} from "react-bootstrap";
import { FaRegEye, FaRegComments } from "react-icons/fa";
import { Link, useHistory } from "react-router-dom";
import { IconContext } from "react-icons";
import { useDispatch, useSelector } from "react-redux";
import {setReceiver} from 'student/store/order'
import moment from "moment";
import { getTranslationUesrsApi } from 'student/http';

import styles from "./UserTable.module.css";
import ImageCustom from "student/components/ImageCustom/ImageCustom";
import { setUserTable } from "student/store/table";


const UserTable = ({role='user'}) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const { user } = useSelector(s=>s.auth);
  const { data: tableData, total: totalData } = useSelector((s) => s.table.userTable);
  const [isUnmount, setIsUnmount] = useState(false);

  const [pagData, setPagData] = useState({
    page: 0,
    row: 5,
  });
  //  console.log('userTable',data)
  React.useEffect(async () => {
    try {
      const { data } = await getTranslationUesrsApi(pagData,role);
      if (data) {
        // console.log('userTable',data)
        if (!isUnmount) {
          if (data.data) {
            dispatch(setUserTable(data));
          }
        }
      }
    } catch (e) {
      console.log(e);
    }

    return () => {
      // setTableData([]);
      setIsUnmount(true);
    };
  }, []);

  const viewClickHandler = (v) => {
    if (user.role !== "user") {
      dispatch(setReceiver(v));
      history.push("/admin/dashboard/inner-user");
    }
  };
  const messageClickHandler = (v) => {};

  const totalPages = Math.ceil(+totalData / +pagData.row);
  const pagNumber = [...Array(totalPages || 5).keys()] || [];
  const pagHandler = (a) => {
    if (a === "next") {
      if (pagData.page !== pagNumber.length - 1)
        setPagData({ ...pagData, page: +pagData.page + 1 });
    } else {
      if (pagData.page >= 1)
        setPagData({ ...pagData, page: +pagData.page - 1 });
    }
  };

  

  return (
    <div className="bg-white rounded overflow-scroll">
      <IconContext.Provider
        value={{ color: "#000", size: 20, paddingLeft: 10 }}
      >
        <Table striped className={styles.table}>
          <thead className={styles.tableInfo}>
            <tr className={styles.tableInfo}>
              <th>Image</th>
              <th>Name</th>
              <th>Email</th>
              <th>Phone</th>
              <th>Country</th>
              <th>Orders</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody
            className={`${styles.tableInfo} ${styles.table} ${styles.tableBody} `}
          >
            {tableData.length ? (
              tableData.map((e) => {
                return (
                  <tr key={e._id} className={styles.tableInfo}>
                    <td>
                      {
                        <ImageCustom
                          src={e.image}
                          alt={e.name}
                          className={styles.profileImage}
                        />
                      }
                    </td>
                    <td>
                      <OverlayTrigger
                        placement="top"
                        overlay={<Tooltip id={`tooltip`}>User Details</Tooltip>}
                      >
                        <Button
                          onClick={() => viewClickHandler(e)}
                          className="text-decoration-none text-dark"
                          variant="link"
                        >
                          {e.name}
                        </Button>
                      </OverlayTrigger>
                    </td>
                    <td>{e.email}</td>
                    <td>{e.phone}</td>
                    <td>{e.country}</td>
                    <td>{e.orders}</td>

                    <td>
                      <div className="d-flex m-0 p-0">
                        <OverlayTrigger
                          placement="top"
                          overlay={<Tooltip id={`tooltip-top`}>view</Tooltip>}
                        >
                          <Button
                            variant="link"
                            className="p-1"
                            onClick={() => viewClickHandler(e)}
                          >
                            <FaRegEye />
                          </Button>
                        </OverlayTrigger>
                        <OverlayTrigger
                          placement="top"
                          overlay={
                            <Tooltip id={`tooltip-top`}>messages</Tooltip>
                          }
                        >
                          <Button
                            variant="link"
                            className="p-1"
                            onClick={() => messageClickHandler(e)}
                          >
                            <FaRegComments />
                          </Button>
                        </OverlayTrigger>
                      </div>
                    </td>
                  </tr>
                );
              })
            ) : (
              <tr className={styles.tableInfo}>
                <td colSpan="5" className="text-center bg-white">
                  There is no Any Order{" "}
                </td>
              </tr>
            )}
          </tbody>
        </Table>
      </IconContext.Provider>
      <Row className="p-1 m-2">
        <Col md={6} className="text-style">
          <p>
            Showing {+pagData.page * +pagData.row} to{" "}
            {(+pagData.page + 1) * pagData.row} of {totalData} entries
          </p>
        </Col>
        <Col md={6}>
          <Pagination>
            <Pagination.Item onClick={() => pagHandler("pre")}>
              {"Privious"}
            </Pagination.Item>
            {+pagData.page > 2 && (
              <>
                <Pagination.Item onClick={() => setPagDataHandler(0)}>
                  {1}
                </Pagination.Item>
                <Pagination.Ellipsis />
              </>
            )}

            {pagData.page > 1 && (
              <Pagination.Item
                onClick={() => setPagDataHandler(+pagData.page - 2)}
              >
                {+pagData.page - 1}
              </Pagination.Item>
            )}
            {pagData.page > 0 && (
              <Pagination.Item
                onClick={() => setPagDataHandler(+pagData.page - 1)}
              >
                {+pagData.page}
              </Pagination.Item>
            )}

            <Pagination.Item
              // onClick={() => setPagData({ ...pagData, page: +e })}
              active
            >
              {+pagData.page + 1}
            </Pagination.Item>
            {+pagData.page <= totalPages - 2 && (
              <Pagination.Item
                onClick={() => setPagDataHandler(+pagData.page + 1)}
              >
                {+pagData.page + 2}
              </Pagination.Item>
            )}

            {+pagData.page <= totalPages - 3 && (
              <Pagination.Item
                onClick={() => setPagDataHandler(+pagData.page + 2)}
              >
                {+pagData.page + 3}
              </Pagination.Item>
            )}

            {+pagData.page <= totalPages - 4 && (
              <>
                <Pagination.Ellipsis />
                <Pagination.Item
                  onClick={() => setPagDataHandler(+totalPages - 1)}
                >
                  {+totalPages}
                </Pagination.Item>
              </>
            )}

            <Pagination.Item onClick={() => pagHandler("next")}>
              {"Next"}
            </Pagination.Item>
          </Pagination>
        </Col>
      </Row>
    </div>
  );
};

export default UserTable;
