import axios from "axios";
import io from "socket.io-client";
import { HOST } from "config.js";

const baseCustunUrl = HOST;

const tApi = axios.create({
  baseURL: baseCustunUrl,
  headers: {
    Authorization: `bearer ${localStorage.getItem("studentToken")}`,
    "Content-Type": "application/json",
  },
});

const aTApi = axios.create({
  baseURL: baseCustunUrl,
  headers: {
    Authorization: `bearer ${localStorage.getItem("adminToken")}`,
    "Content-Type": "application/json",
  },
});

const api = axios.create({
  baseURL: baseCustunUrl,
  headers: {
    "Content-Type": "application/json",
  },
});
const formApi = axios.create({
  baseURL: baseCustunUrl,
  headers: {
    Authorization: `bearer ${localStorage.getItem("studentToken")}`,
  },
});
export const socket = io.connect(baseCustunUrl, {
  transports: ["websocket"],
  upgrade: false,
  reconnectionAttempts: 3,
});


export const getAllNotificationApi = (i, d) =>
  api.get(`/api/notification/all/${i}?page=${d.page}&row=${d.row}`);
export const refreshApi = () => tApi.get("/api/refresh");
export const getNotificationApi = (i)=>api.get(`/api/notification/${i}`);
export const setSeenNotificationApi = (i)=>api.get(`/api/notification/seen/${i}`);
export const forgotApi = (d)=>api.post('/api/forgot/otp',d);
export const forgotVerifyApi = (d)=>api.post('/api/forgot/verify',d);
export const getUnseenMessages = i => api.get(`/api/message/unseen/user/${i}`);
export const setSeenOrderApi = (i) => api.get(`/api/message/unseen/order/${i}`);
export const getAdimnApi = ()=> api.get('/api/admin');
export const postOrderApi = (d) => formApi.post('/api/mainorder',d);
export const postTranslationOrderApi = (d) => formApi.post("/api/translation", d);
export const loginApi = (d) => api.post('/api/login',d);
export const getOrderMessageApi =(i)=>api.get(`/api/message/order/${i}`);
export const getUserTranslationOrderApi = (i, d) =>api.get(`/api/translation/user/${i}?page=${d.page}&row=${d.row}`);
export const getUserOrderApi = (i,d) => api.get(`/api/mainorder/user/${i}?page=${d.page}&row=${d.row}`)
export const getUserTransitionOrderSearchApi = (i, d, s) =>
  api.get(
    `/api/translation/filter/user/${i}?page=${d.page}&row=${d.row}&country=${s.country}&service=${s.service}&translation=${s.translation}&deadline=${s.deadline}`
  );
export const getUserChatOrderApi = (i, filter) =>
  api.get(`/api/translation/chat-order/${i}?${filter}`);
export const getTranSearchApi = (d, s) =>
    api.get(`/api/translation/filter/search?translation=${s.translation}`);


export const refreshAdminApi = () => aTApi.get("/api/refresh");
export const adminLoginApi = (d) => api.post("/api/admin/login", d);
export const getAllUserApi = () => api.get("/api/users");
export const getAllOrderApi =(d,user)=>aTApi.get(`/api/translation?page=${d.page}&row=${d.row}&user=${user}`);
export const getTranslationUesrsApi = (d, user) =>
  api.get(`/api/users/order?page=${d.page}&row=${d.row}&user=${user}`);
export const getChatOrderApi = (i,filter) => api.get(`/api/translation/chat-order/admin/${i}?${filter}`);
export const getAdTransitionOrderSearchAdminApi = (d, s) =>
  api.get(
    `/api/translation/filter/order?page=${d.page}&row=${d.row}&country=${s.country}&service=${s.service}&translation=${s.translation}&deadline=${s.deadline}`
  );

  


