import { createSlice } from "@reduxjs/toolkit";

const authSlice = createSlice({
  name: "auth",
  initialState: {
    forgot: null,
    isAuth: !!localStorage.getItem("studentToken") || false,
    isAdminAuth: !!localStorage.getItem("adminToken") || false,
    admin: null,
    user: {
      _id: "",
      name: "",
      email: "",
      image: "",
    },
  },
  reducers: {
    setLogIn: (state, action) => {
      state.user = action.payload.user;
      if (state.user._id !== "" && state.admin === null) {
        state.isAuth = true;
        localStorage.setItem("studentToken", action.payload.token);
                localStorage.setItem(
                  "user",
                  JSON.stringify(action.payload.user)
                );

      } else {
        state.isAuth = false;
      }
    },
    setAdminLogIn: (state, action) => {
      state.admin = action.payload.user;
      state.user = action.payload.user;
      if (state.admin !== null) {
        localStorage.setItem("adminToken", action.payload.token);
        localStorage.setItem("user",JSON.stringify(action.payload.user));
        state.isAdminAuth = true;
      } else {
        state.isAdminAuth = false;
      }
    },
    setLogOut: (state, action) => {
      state.user = null;
      state.isAuth = false;
      state.admin = null;
      state.isAdminAuth = false;
      localStorage.clear();
    },
    setForgot: (state, action) => {
      state.forgot = action.payload;
    },
    setUser: (state, action) => {
      state.user = action.payload;
    },
  },
});

export const { setLogIn, setLogOut, setUser, setForgot, setAdminLogIn } =
  authSlice.actions;

export default authSlice.reducer;
