import React, { useState, useEffect } from "react";
import styles from "./Search.module.css";
import {
  Col,
  Row,
  Table,
  Button,
  Collapse,
  Card,
  Form,
  OverlayTrigger,
  Tooltip,
  Pagination,
} from "react-bootstrap";
import { FaSearch, FaSyncAlt, FaPaste, FaTachometerAlt } from "react-icons/fa";
import {
  getUserTransitionOrderSearchApi,
  getAdTransitionOrderSearchAdminApi,
} from "student/http";
import { useSelector } from "react-redux";
import { FaRegEye, FaRegComments } from "react-icons/fa";
import { IconContext } from "react-icons";
import moment from "moment";
import { Link, useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { setOrder } from "student/store/order";
import { setReceiver } from "student/store/order";



const Search = ({ text = "Dashboard" }) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const [open, setOpen] = React.useState(false);
  const [datalist, setDataList] = useState([]);
  const { user } = useSelector((s) => s.auth);
  const [search, setSearch] = useState({
    country: "",
    service: "",
    deadline: "",
    translation: "",
  });
  const [tableData, setTableData] = useState([]);
  const [totalData, setTotalData] = useState(5);
  const [pagData, setPagData] = useState({
    page: 0,
    row: 5,
  });

   const pagNumber =
     [...Array(Math.ceil(+totalData / +pagData.row) ?? 10).keys()] || [];

   const pagHandler = (a) => {
     if (a === "next") {
       if (pagData.page !== pagNumber.length - 1)
         setPagData({ ...pagData, page: +pagData.page + 1 });
     } else {
       if (pagData.page >= 1)
         setPagData({ ...pagData, page: +pagData.page - 1 });
     }
   };


  useEffect(() => {
    if (search.country === "" && search.service === "" && search.translation === "" && search.deadline === '') return;
    try {
      if (user.role === "user") {
        (async () => {
          await getUserTransitionOrderSearchApi(user._id, pagData, search)
            .then((res) => {
              console.log(res.data);
              if (res.data?.data) {
                setTableData(res.data.data);
                setTotalData(res.data.total);
              } else {
                setTableData([]);
                setTotalData(0);
              }
            })
            .catch((err) => {
              console.log(err);
            });
        })();
      } else {
        (async () => {
          await getAdTransitionOrderSearchAdminApi(pagData, search)
            .then((res) => {
              console.log(res.data);
              if (res.data?.data) {
                setTableData(res.data.data);
                setTotalData(res.data.total);
              } else {
                setTableData([]);
                setTotalData(0);
              }
            })
            .catch((err) => {
              console.log(err);
            });
        })();
      }
    } catch (err) {
      console.log(err);
    }
    return () => {};
  }, [search, pagData]);


  const viewClickHandler = (v) => {
    dispatch(setOrder(v));
    if(user.role !== 'user'){
      dispatch(setReceiver({_id: v.userId}))
    }
    const url =
      user.role === "admin"
        ? "/admin/dashboard/inner-order"
        : "/dashbord/inner-order";
    history.push(url);
  };
  const messageClickHandler = (v) => {
    dispatch(setOrder(v));
    if (user.role !== "user") {
      dispatch(setReceiver({ _id: v.userId }));
    }
    const url =
      user.role === "admin"
        ? "/admin/dashboard/chat-support"
        : "/dashbord/chat-support";
    history.push(url);
  };

  return (
    <div>
      <Row>
        <Col md={6}>
          <h4 className={styles.dashboard}>{text}</h4>
        </Col>
        <Col md={6}>
          <div className={styles.btnRight}>
            <Button
              className={styles.dashBoardBtn}
              onClick={() => setOpen(!open)}
              aria-controls="example-collapse-text"
              aria-expanded={open}
            >
              <FaSearch />
            </Button>
          </div>
        </Col>
      </Row>
      <Collapse in={open} className="mt-2">
        <div id="example-collapse-text">
          <Card>
            <Card.Body>
              <Row>
                <Col md={5}>
                  <Row>
                    <Col md={6} className="p-1">
                      <Form.Control
                        placeholder="Enter Translation Id"
                        type='text'
                        onChange={(e) =>
                          setSearch({ ...search, translation: e.target.value })
                        }
                      />
                    </Col>
                    <Col md={6} className="p-1">
                      <Form.Control
                        placeholder="Enter Date"
                        onChange={(e) =>
                          setSearch({ ...search, deadline: e.target.value })
                        }
                        type="date"
                      />
                    </Col>
                  </Row>
                </Col>
                <Col md={5}>
                  <Row>
                    <Col md={6} className="p-1">
                      <Form.Control
                        placeholder="Enter Contry"
                        onChange={(e) =>
                          setSearch({ ...search, country: e.target.value })
                        }
                      />
                    </Col>
                    <Col md={6} className="p-1">
                      <Form.Control
                        placeholder="Service"
                        onChange={(e) =>
                          setSearch({ ...search, service: e.target.value })
                        }
                        type="text"
                      />
                    </Col>
                  </Row>
                </Col>
                <Col md={2} className="m-0 px-2 py-1 row">
                  <Row>
                    <Col xs={6} md={6} className="m-0">
                      <Button className={styles.searchBtn}>
                        <FaSearch />
                      </Button>
                    </Col>
                    <Col xs={6} md={6}>
                      <Button className={styles.refreshBtn}>
                        <FaSyncAlt />
                      </Button>
                    </Col>
                  </Row>
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </div>
      </Collapse>

      {tableData.length !== 0 && (search.service || search.country || search.translation || search.deadline) && (
        <div className="bg-white rounded overflow-scroll">
          <IconContext.Provider
            value={{ color: "#000", size: 20, paddingLeft: 10 }}
          >
            <Table striped className={styles.table}>
              <thead className={styles.tableInfo}>
                <tr className={styles.tableInfo}>
                  <th>Translation Id</th>
                  <th>Deadline</th>
                  <th>country</th>
                  <th>Source Language</th>
                  <th>Service Required</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody
                className={`${styles.tableInfo} ${styles.table} ${styles.tableBody} `}
              >
                {tableData.length ? (
                  tableData.map((e) => {
                    return (
                      <tr key={e._id} className={styles.tableInfo}>
                        <td>{e.translationId}</td>
                        <td>{moment(e.deadline).format("DD/MM/YYYY")}</td>
                        <td>{e.country}</td>
                        <td>{e.sourceLanguage}</td>
                        <td>{e.service_req}</td>
                        <td>
                          <OverlayTrigger
                            placement="top"
                            overlay={<Tooltip id={`tooltip-top`}>view</Tooltip>}
                          >
                            <Button
                              variant="link"
                              onClick={() => viewClickHandler(e)}
                            >
                              <FaRegEye />
                            </Button>
                          </OverlayTrigger>
                          <OverlayTrigger
                            placement="top"
                            overlay={
                              <Tooltip id={`tooltip-top`}>messages</Tooltip>
                            }
                          >
                            <Button
                              variant="link"
                              onClick={() => messageClickHandler(e)}
                            >
                              <FaRegComments />
                            </Button>
                          </OverlayTrigger>

                          <Link to="#"></Link>
                        </td>
                      </tr>
                    );
                  })
                ) : (
                  <tr className={styles.tableInfo}>
                    <td colSpan="5" className="text-center bg-white">
                      There is no Any Order{" "}
                    </td>
                  </tr>
                )}
              </tbody>
            </Table>
          </IconContext.Provider>
          <Row className="p-1 m-2">
            <Col md={6} className="text-style">
              <p>
                Showing {+pagData.page * +pagData.row} to{" "}
                {(+pagData.page + 1) * pagData.row} of {totalData} entries
              </p>
            </Col>
            <Col md={6}>
              <Pagination>
                <Pagination.Item onClick={() => pagHandler("pre")}>
                  {"Privious"}
                </Pagination.Item>
                {pagNumber.map((e) => {
                  return (
                    <Pagination.Item
                      onClick={() => setPagData({ ...pagData, page: +e })}
                      active={pagData.page === +e}
                      key={e}
                    >
                      {+e + 1}
                    </Pagination.Item>
                  );
                })}
                <Pagination.Item onClick={() => pagHandler("next")}>
                  {"Next"}
                </Pagination.Item>
              </Pagination>
            </Col>
          </Row>
        </div>
      )}
    </div>
  );
};

export default Search;
