import { createSlice } from "@reduxjs/toolkit";
import { setSeenNotificationApi } from "student/http";

const notificationSlice = createSlice({
  name: "notification",
  initialState: {
    notifications: [],
  },
  reducers: {
    setNotification: (s, a) => {
      s.notifications = a.payload;
    },
    addNotification: (s, a) => {
      s.notifications.unshift(a.payload);
    },
    rmNotification: (s, a) => {
        s.notifications = s.notifications.filter(e=>e._id !== a.payload._id);
      try {
        setSeenNotificationApi(a.payload._id);
      } catch (err) {
        console.log(err);
      }
    },
  },
});

export const { setNotification, addNotification, rmNotification } = notificationSlice.actions;

export default notificationSlice.reducer;
