import React from 'react'
import { Row, Col, ListGroup } from 'react-bootstrap'
import { FaScroll, FaCommentDots, FaLock, FaUserGraduate, FaShieldAlt, FaClock, FaUserEdit } from 'react-icons/fa'
import { IconContext } from 'react-icons';

import styles from './AddOrder.module.css';
import OrderForm from 'student/components/OrderForm/OrderForm';
import TranslationForm from 'student/components/TranslationForm/TranslationForm';

const AddOrder = () => {
    return (
        <div className={styles.bgLightWhite}>
            <Row>
                <Col md={8}>
                    <div className={`${styles.chatFormHead} py-2 shadow`} >
                        <h4 className='text-white'>Submit New Translation</h4>
                    </div>
                    <div className={`${styles.chatFormBody} pb-4 shadow`}>
                        <TranslationForm />
                    </div>
                </Col>
                <Col md={4} >
                    <img className='img-fluid d-none d-sm-block' src='/images/order.png' />
                    <div className={`${styles.orangeBg} mt-3 text-white`}>
                        <h6>
                            WHY STUDENTS PREFER US ?
                        </h6>
                        <IconContext.Provider value={{ color: "#fff", size: 25 }}>
                        <ListGroup className='text-white'>
                            <ListGroup.Item className='bg-transparent text-white' > <FaScroll />  Top Quality Paper</ListGroup.Item>
                            <ListGroup.Item className='bg-transparent text-white'><FaCommentDots />  24/7 Supports</ListGroup.Item>
                            <ListGroup.Item className='bg-transparent text-white'><FaLock />  100% Privacy</ListGroup.Item>
                            <ListGroup.Item className='bg-transparent text-white' ><FaUserGraduate />  1500 + PhD EXPERTS</ListGroup.Item>
                            <ListGroup.Item className='bg-transparent text-white' ><FaShieldAlt />  100% Confidential</ListGroup.Item>
                            <ListGroup.Item className='bg-transparent text-white' ><FaClock />  On-Time Delivery</ListGroup.Item>
                            <ListGroup.Item className='bg-transparent text-white' ><FaUserEdit />  Native USA Writers</ListGroup.Item>
                        </ListGroup>
                        </IconContext.Provider>

                    </div>

                </Col>


            </Row>
        </div>
    )
}

export default AddOrder
