import React from "react";
import { useHistory } from "react-router-dom";
import { Badge, Button, ListGroup, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { setOrder } from "student/store/order";
import { setReceiver } from "student/store/order";
import { rmOByM } from "student/store/order";
import PerfectScrollbar from "react-perfect-scrollbar";

const ListGrout = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const { orderByMessage } = useSelector((s) => s.order);
  const { user } = useSelector((s) => s.auth);
  const ps = React.useRef();

 console.log(orderByMessage,'orderByMessage');

  const listOnClickHandler = (e) => {
    const promise = new Promise(r=>{
      //  dispatch(rmOByM(e));
       dispatch(setOrder(e));
       r()
    })
    promise.then(()=>{
       if (user.role === "user") {
         history.push("/dashbord/chat-support");
       } else {
         dispatch(setReceiver({ _id: e.userId }));
         history.push("/admin/dashboard/chat-support");
       }
    })
  };
  const data = orderByMessage.filter((e) => e.unseenMessages.some((e) => e));
  return ( data.length > 0 &&  
    <ListGroup as="ol" className="rounded" numbered>
      <PerfectScrollbar
        style={{ height: "60vh" }}
        containerRef={(el) => (ps.current = el)}
      >
        {data
          .map((e) => (
            <ListGroup.Item
              key={e._id}
              action
              onClick={() => listOnClickHandler(e)}
              className="d-flex flex-column"
            >
              <div className=" w-100 m-0 d-flex flex-row justify-content-between">
                <div className="me-5" style={{ lineHeight: 1 }}>
                  <div className="fw-bold">{e.country}</div>
                 
                    message:
                    <ol>
                      {e.unseenMessages.map((e, i) => (
                        <li key={i}>{`${e.message || 'file'}`}</li>
                      ))}
                    </ol>
                
                </div>
                <div>
                  <Badge variant="danger" pill>
                    {e.unseenMessages.length}
                  </Badge>
                </div>
              </div>
              <div>
                <p className="p-0 m-0">Order Id: {e?.translationId}</p>
              </div>
            </ListGroup.Item>
          ))}
      </PerfectScrollbar>
    </ListGroup>
  );
};

export default ListGrout;
