import {
  FaTachometerAlt,
  FaRegCommentDots,
  FaUserEdit,
  FaCartArrowDown,
  FaRegBell,
  FaRegAddressBook,
} from "react-icons/fa";

import Dashboard from "student/admin/pages/Dashboard/DashBoard";
import ChatSupport from "student/pages/dashboard/ChatSupport/ChatSupport";
import AddOrder from "student/pages/dashboard/AddOrder/AddOrder";
import Notification from "student/pages/dashboard/Notification/Notification";
import AddUserForm from 'student/components/UserForm/AddUserForm/AddUserForm';
import UserTable from "student/components/Table/UserTable/UserTable";
import TableBoot from "student/components/Table/TableBoot";
import InnerOrder from "student/pages/dashboard/InnerOrder/InnerOrder";
import DLayout from "student/layout/DLayout";
import InnerUser from "student/admin/pages/InnerUser/InnerUser";



const adminRoutes = [
  {
    name: "Dashbord",
    path: "/dashbord",
    icon: <FaTachometerAlt />,
    component: Dashboard,
  },
  {
    name: "Users",
    path: "/users",
    icon: <FaRegAddressBook />,
    children: [
      {
        name: "Client",
        path: "/",
        icon: <FaTachometerAlt />,
        component: <UserTable />,
      },
      {
        name: "Sub Admin",
        path: "/sub-admin",
        icon: <FaTachometerAlt />,
        component: <UserTable />,
      },
      {
        name: "Add New User",
        path: "/add-user",
        icon: <FaTachometerAlt />,
        component: <AddUserForm />,
      },
    ],
  },
  {
    name: "Order",
    path: "/order",
    icon: <FaUserEdit />,
    component: TableBoot,
    children: [
      {
        name: "New Order",
        path: "/",
        icon: <FaTachometerAlt />,
        component: <TableBoot />,
      },
      {
        name: "Paid Orders",
        path: "/paid-orders",
        icon: <FaTachometerAlt />,
        component: <TableBoot />,
      },
      {
        name: "Add New Order",
        path: "/add-order",
        icon: <FaTachometerAlt />,
        component: <TableBoot />,
      },
    ],
  },
  {
    name: "Chat Support",
    path: "/chat-support",
    icon: <FaRegCommentDots />,
    component: ChatSupport,
  },
  {
    path: "/inner-order",
    icon: <FaRegBell />,
    component: InnerOrder,
  },
  {
    path: "/inner-user",
    icon: <FaRegBell />,
    component: InnerUser,
  },
  {
    name: "Notification",
    path: "/notification",
    icon: <FaRegBell />,
    component: Notification,
  },
];

export default adminRoutes;
