
import { Children, useState } from 'react';
import { Button, Col, Modal } from 'react-bootstrap';
import styles from './Model.module.css'; 
import {FaPlus} from 'react-icons/fa'

function Example({buttonText='Button',className='', children=''}) {
    const [show, setShow] = useState(false);
  
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
  
    return (
      <>
        <Button variant="primary" className={className} onClick={handleShow}>
          {buttonText}
        </Button>

        <Modal show={show} onHide={handleClose}>
          <Button
          variant='danger'
            style={{ right: 0 ,zIndex: 1 }}
            className="m-2 mt-3 position-absolute"
            onClick={handleClose}
          >
            <FaPlus style={{ transform: "rotate(45deg)", fontSize: 20 }} />
          </Button>
          <Modal.Header closeButton className={`text-white ${styles.header}`}>
            <Modal.Title>Edit Profile</Modal.Title>
          </Modal.Header>
          <Modal.Body>{children}</Modal.Body>
        </Modal>
      </>
    );
  }
  
 export default Example