import React, { useState } from "react";
import { Row, Col, Form, Button, Container } from "react-bootstrap";
import styles from "./Login.module.css";
import { loginApi } from "student/http";
import { useDispatch } from "react-redux";
import { setLogIn } from "student/store/auth";
import { useHistory, NavLink } from "react-router-dom";
import { toast } from "react-toastify";

const Login = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const [validated, setValidated] = useState(false);
  const [isUnmount, setIsUnmount] = useState(false);
  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });

  const handleSubmit = async (event) => {
    event.preventDefault();
    const form = event.currentTarget;
    if (form.checkValidity() === true) {
      try {
        const tId = toast.loading("Please wait...");
        const req = await loginApi(formData)
          .then((res) => {
            if (!isUnmount) {
              dispatch(setLogIn(res.data));
              toast.update(tId, {
                render: res.data.message,
                type: "success",
                isLoading: false,
                closeButton: true,
                autoClose: 5000,
              });
              history.push("/");
            }
          })
          .catch(function (error) {
            toast.update(tId, {
              render: error.response.data?.message,
              type: "error",
              isLoading: false,
              closeButton: true,
              autoClose: 5000,
            });
          });
      } catch (e) {
        console.log(e);
      }
    }
    setValidated(true);
  };
  React.useEffect(() => {
    return () => {
      setIsUnmount(true);
    };
  }, []);
  return (
    <div className={`${styles.loginBody} p-2 min-vh-100  `}>
      <div className={styles.bgImg}>
        <Container>
          <Row>
            <Col md={1}></Col>
            <Col md={10} className={styles.bgWhite}>
              <Row className="m-0">
                <Col className="p-0" md={6}>
                  <div className={`${styles.blueBg} text-white`}>
                    <div>
                      <h2 className={`${styles.heading}`}>Account Login</h2>
                    </div>
                    <div>
                      <h5>See what's going on in your assignment</h5>
                      <div className={styles.lineWhite}></div>
                    </div>
                    <Form
                      noValidate
                      validated={validated}
                      onSubmit={handleSubmit}
                    >
                      <div className={styles.formSection}>
                        <Form.Group
                          className="mb-3 text-white"
                          controlId="exampleForm.ControlInput1"
                        >
                          <Form.Label className="text-white">
                            Email address
                          </Form.Label>
                          <Form.Control
                            type="email"
                            placeholder="name@example.com"
                            value={formData.email}
                            onChange={(e) =>
                              setFormData({
                                ...formData,
                                email: e.target.value,
                              })
                            }
                            name="email"
                            required
                          />
                          <Form.Control.Feedback type="invalid">
                            please enter a valid email
                          </Form.Control.Feedback>
                        </Form.Group>
                        <Form.Group
                          className="mb-3"
                          controlId="formBasicPassword"
                        >
                          <Form.Label className="text-white">
                            Password
                          </Form.Label>
                          <Form.Control
                            type="password"
                            placeholder="Password"
                            name="password"
                            onChange={(e) =>
                              setFormData({
                                ...formData,
                                password: e.target.value,
                              })
                            }
                            value={formData.password}
                            required
                          />
                          <Form.Control.Feedback type="invalid">
                            Please Enter a Password.
                          </Form.Control.Feedback>
                        </Form.Group>
                        <Row>
                          <Form.Group
                            as={Col}
                            md={6}
                            className="mb-3"
                            controlId="formBasicCheckbox"
                          >
                            <Form.Check type="checkbox" label="Remember me" />
                          </Form.Group>
                          <Col>
                            <NavLink
                              className="text-white"
                              style={{ textDecoration: "none" }}
                              to="forgot"
                            >
                              <p>forgot Password</p>
                            </NavLink>
                          </Col>
                        </Row>
                      </div>
                      <div className="text-center">
                        <Button
                          className={`${styles.btnPrimary} px-5 `}
                          type="submit"
                        >
                          Login
                        </Button>
                      </div>
                    </Form>
                  </div>
                </Col>
                <Col
                  md={6}
                  className="side-img position-relative d-none d-sm-block"
                >
                  <img
                    src={"/images/reviews.png"}
                    alt="Login"
                    className="img-fluid position-absolute top-50 start-50 translate-middle"
                  />
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
};

export default Login;
