import React from 'react'
import { Switch, Route, Redirect } from "react-router-dom";
import Reset from "student/pages/reset/Reset";
import Forgot from 'student/pages/forgot/Forgot';
import Layout from "student/pages/dashboard";
import Login from 'student/admin/pages/login/Login'
import { useSelector } from "react-redux";
import adminRoutes from 'student/admin/adminRoutes'



const Admin = () => {
    return (
      <Switch>
        <PublicRouts path="/admin/login">
          <Login isAdmin={true} />
        </PublicRouts>
        <PrivateAdminRouet path="/admin/dashboard">
          <Layout routers={adminRoutes} />
        </PrivateAdminRouet>
        <PublicRouts path="/admin/forgot">
          <Forgot />
        </PublicRouts>
        <ForgotRouts path="/admin/reset">
          <Reset />
        </ForgotRouts>
        <Route path="/admin">
          <Redirect to="/admin/dashboard" />
        </Route>
      </Switch>
    );
}

const PrivateAdminRouet = ({ children, ...rest }) => {
  const { isAdminAuth } = useSelector((state) => state.auth);
  return (
    <Route
      {...rest}
      render={({ location }) => {
        return isAdminAuth ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/admin/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
};

const ForgotRouts = ({ children, ...rest }) => {
  const { isAdminAuth, forgot } = useSelector((state) => state.auth);

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return !isAdminAuth && forgot ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/admin/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
};

const PublicRouts = ({ children, ...rest }) => {
  const { isAdminAuth } = useSelector((state) => state.auth);

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return !isAdminAuth ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/admin/dashboard",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
};

export default Admin
