import React, { useState, useEffect } from "react";
import styles from "./Chat.module.css";
import {
  Container,
  Row,
  Col,
  Form,
  Button,
  Badge,
  ToggleButton,
  Alert,
} from "react-bootstrap";
import {
  FaSearch,
  FaFolderPlus,
  FaPaperPlane,
  FaDownload,
  FaChevronDown,
  FaFileDownload,
  FaFile,
} from "react-icons/fa";
import { IconContext } from "react-icons";
import { getOrderMessageApi } from "student/http";
import { useDispatch, useSelector } from "react-redux";
import { socket } from "student/http";
import PerfectScrollbar from "react-perfect-scrollbar";
import moment from "moment";
import SelectFileModal from "student/components/SelectFileModal/SelectFileModal";

import ImageCustom from "student/components/ImageCustom/ImageCustom";

const Chat = () => {
  const { user } = useSelector((s) => s.auth);
  const { order, receiver } = useSelector((s) => s.order);
  const dispatch = useDispatch();

  const [recentMessage, setRecentMessage] = useState("");
  const [showImageModal, setShowImageModal] = useState(false);
  const [textValue, setTextValue] = useState("");
  const [files, setFiles] = useState([]);
  const [messages, setMessages] = useState([]);
  const [error, setError] = useState(null);
  const [dropdownToggle, setDropdownToggle] = useState(true);
  const ps = React.useRef();

  console.log("receiver", receiver);

  useEffect(async () => {
    if (order._id) {
      try {
        const { data } = await getOrderMessageApi(order?._id);
        if (data) {
          setMessages(data.data.reverse());
        }
      } catch (error) {
        console.log(error);
      }
    }
    return () => {
      setMessages([]);
    };
  }, [order]);
  // console.log('messages', messages);

  React.useEffect(() => {
    const curr = ps.current;
    if (curr) {
      curr.scrollTop = curr.scrollHeight;
      setDropdownToggle(false);
    }
  }, [messages]);

  useEffect(async () => {
    socket.on("orderMessage", (data) => {
      setRecentMessage(data);
      // console.log('soketDat', data);
    });
    socket.on("fileError", (data)=>{
      setError(data);
      console.log('fileError');
    });
  }, [socket]);

  useEffect(() => {
    if (recentMessage) {
      if (recentMessage.orderId !== order._id) return;
      setMessages((pre) => [
        ...pre,
        {
          ...recentMessage,
          _id: Math.random(),
          message: recentMessage.text,
        },
      ]);
    }
    if(error){
      setError(null);
    }
  }, [recentMessage]);

  const sendClickHandler = () => {

    if (!order._id) return;
    if (!textValue) return;
    const chatData = {
      text: textValue,
      sender: user,
      _id: user._id,
      receiver: receiver._id,
      orderId: order._id,
      order,
    };
    socket.emit("chat", chatData);
    setTextValue("");
  };
  const onKeyEnterHandler = (e) => {
    if (e.key === "Enter" && e.shiftKey === false) {
      e.preventDefault();
      sendClickHandler();
    }
  };

  const setTextValueHandler = (e) => {
    const value = e.target.value;
    setTextValue(value);
  };
  const fileChangeHandler = (e) => {
    const dfiles = e.target.files;
    setFiles(Array.from(dfiles));
    setShowImageModal(true);
  };
  const downloadeHandler = (f) => {
    fetch(f)
      .then((response) => {
        response.arrayBuffer().then(function (buffer) {
          const url = window.URL.createObjectURL(new Blob([buffer]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", f.substr(f.lastIndexOf("-") + 1)); //or any other extension
          document.body.appendChild(link);
          link.click();
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const scrollDownClickHandler = () => {
    const curr = ps.current;
    if (curr) {
      curr.scrollTop = curr.scrollHeight;
      setDropdownToggle(false);
    }
  };
  if (!order?._id) {
    return (
      <div>
        <div className={`${styles.chatFormHead} py-2 shadow`}>
          <h5 className="text-white">Select an Order to Chat</h5>
        </div>
        <div className={`${styles.chatFormBody} shadow`}>
          <Row className={`${styles.rightSideChat} m-0 py-1 `}>
            <div className="d-flex justify-content-center ">
              <div className="m-0 p-0">
                <p className={styles.chatText}>Select an Order To Chat</p>
              </div>
            </div>
          </Row>
        </div>
        <div className={styles.darkOrenge}>
          <Container>
            <Row>
              <div className={`py-2 shadow`}>
                <h5 className="text-white">Select an Order to Chat</h5>
              </div>
            </Row>
          </Container>
        </div>
      </div>
    );
  }
  return (
    <div>
      <div className={`${styles.chatFormHead} py-2 shadow`}>
        <h5 className="text-white">
          Order Id: <span className="text-danger fw-bold">{order?.translationId}</span>
        </h5>
      </div>

      <div className={`${styles.chatFormBody} shadow`}>
        <PerfectScrollbar containerRef={(el) => (ps.current = el)}>
          {messages.length ? (
            messages.map((e, i) => (
              <div key={`${e._id}-${i}`}>
                {user._id === e.sender?._id ? (
                  <Row className={`${styles.rightSideChat} m-0 py-1 `}>
                    <div className="d-flex justify-content-end ">
                      <div className="m-0 p-0 ">
                        {e.message && (
                          <p className={styles.chatText}>{e.message}</p>
                        )}
                        {e.file?.length
                          ? e.file.map((f) => {
                              return (
                                <div
                                  className="justify-content-end position-relative text-end d-flex"
                                  key={f}
                                >
                                  {/.*\.(gif|jpe?g|bmp|png|webp)$/gim.test(
                                    f
                                  ) ? (
                                    <>
                                      <div className="w-75">
                                        <img
                                          src={f}
                                          alt="FileImage"
                                          width={100}
                                          height={100}
                                          className="img-fluid rounded text-end mt-1"
                                        />
                                      </div>
                                      <Badge
                                        as="button"
                                        className={styles.bedge}
                                        onClick={() => downloadeHandler(f)}
                                        pill
                                      >
                                        <FaDownload />
                                      </Badge>
                                    </>
                                  ) : (
                                    <div
                                      style={{
                                        marginLeft: 180,
                                      }}
                                    >
                                      <Row className="mt-3">
                                        <Col sm={9}>
                                          <p>
                                            {f.substr(f.lastIndexOf("-") + 1)}
                                          </p>
                                        </Col>
                                        <Col sm={3}>
                                          <FaFileDownload
                                            style={{ fontSize: 30 }}
                                            onClick={() => downloadeHandler(f)}
                                          />
                                        </Col>
                                      </Row>
                                    </div>
                                  )}
                                </div>
                              );
                            })
                          : ""}
                        <div className="m-0">
                          <span dir="auto" className={`${styles.chatTextTime}`}>
                            {moment(e.createdAt).calendar()}
                          </span>
                        </div>
                      </div>
                      <div>
                        <ImageCustom
                          src={e?.sender?.image}
                          className={styles.chatTextImage}
                          alt={e?.sender?.name}
                        />
                      </div>
                    </div>
                  </Row>
                ) : (
                  <Row className={`${styles.leftSideChat}  m-0 py-1`}>
                    <div className="d-flex justify-content-start ">
                      <div>
                        <ImageCustom
                          src={e?.sender?.image}
                          className={styles.chatTextImage}
                          alt={e?.sender?.name}
                        />
                      </div>
                      <div className="m-0 p-0">
                        {e.message && (
                          <p className={`${styles.chatText} text-start`}>
                            {e.message}
                          </p>
                        )}
                        {e.file?.length
                          ? e.file.map((f) => {
                              return (
                                <div
                                  className="justify-content-start position-relative text-end d-flex"
                                  key={f}
                                >
                                  {/.*\.(gif|jpe?g|bmp|png)$/gim.test(f) ? (
                                    <>
                                      <div className="w-75">
                                        <img
                                          src={f}
                                          alt="FileImage"
                                          width={100}
                                          height={100}
                                          className="img-fluid rounded text-end mt-1"
                                        />
                                      </div>
                                      <Badge
                                        as="button"
                                        className={styles.bedge}
                                        onClick={() => downloadeHandler(f)}
                                        pill
                                      >
                                        <FaDownload />
                                      </Badge>
                                    </>
                                  ) : (
                                    <div>
                                      <Row className="mt-3">
                                        <Col sm={3}>
                                          <FaFileDownload
                                            style={{ fontSize: 30 }}
                                            onClick={() => downloadeHandler(f)}
                                          />
                                        </Col>
                                        <Col sm={9}>
                                          <p>
                                            {f.substr(f.lastIndexOf("-") + 1)}
                                          </p>
                                        </Col>
                                      </Row>
                                    </div>
                                  )}
                                </div>
                              );
                            })
                          : ""}
                        <div className="mt-0">
                          <span dir="auto" className={`${styles.chatTextTime}`}>
                            {moment(e.createdAt).calendar()}
                          </span>
                        </div>
                      </div>
                    </div>
                  </Row>
                )}
              </div>
            ))
          ) : (
            <Row className={`${styles.rightSideChat} m-0 py-1 `}>
              <div className="d-flex justify-content-center ">
                <div className="m-0 p-0">
                  <p className={styles.chatText}>Ask Your Queries</p>
                </div>
              </div>
            </Row>
          )}
        </PerfectScrollbar>
        {error && (
          <Alert
            style={{
              position: "absolute",
              textAlign: "center",
              width: "100%",
              bottom: 0,
              margin: "auto",
            }}
            variant={"danger"}
          >
            {error.message}
          </Alert>
        )}

        {
          <Button
            variant="outline-dark"
            size="sm"
            onClick={scrollDownClickHandler}
            style={{
              position: "absolute",
              marginTop: -50,
              right: 30,
              borderRadius: "50%",
            }}
          >
            <FaChevronDown />
          </Button>
        }
      </div>
      <div className={styles.darkOrenge}>
        <IconContext.Provider value={{ color: "#fff", size: 30 }}>
          <Container>
            <Row>
              <Col md={1} sm={1} className="p-0 mt-2">
                <SelectFileModal
                  show={showImageModal}
                  data={{ user, order, receiver, files }}
                  onHide={() => setShowImageModal(false)}
                />
                <label htmlFor="attach" className="">
                  <FaFolderPlus />
                  <input
                    type="file"
                    id="attach"
                    onChange={fileChangeHandler}
                    name="attach"
                    multiple={true}
                    className="d-none"
                  />
                </label>
              </Col>
              <Col md={10} sm={10} className="py-1 mr-0">
                <Form>
                  <Form.Group controlId="exampleForm.ControlTextarea1">
                    <Form.Control
                      as="textarea"
                      rows={1}
                      onKeyDown={onKeyEnterHandler}
                      onChange={setTextValueHandler}
                      value={textValue}
                      placeholder="Type Somthing"
                    />
                  </Form.Group>
                </Form>
              </Col>
              <Col md={1} sm={1} className=" position-relative">
                <Button
                  className={`${styles.btnSend} mt-1 position-absolute`}
                  onClick={sendClickHandler}
                  variant="link"
                >
                  <IconContext.Provider value={{ color: "#fff", size: 25 }}>
                    <FaPaperPlane />
                  </IconContext.Provider>
                </Button>
              </Col>
            </Row>
          </Container>
        </IconContext.Provider>
      </div>
    </div>
  );
};

export default Chat;
