import React, { useState, useEffect } from 'react'
import { Row, Col, Form, Button, Container } from 'react-bootstrap'
import styles from './Reset.module.css'
import { useSelector } from 'react-redux'
import { forgotVerifyApi } from 'student/http'
import { setForgot } from 'student/store/auth'
import { useDispatch } from 'react-redux'

import { Link, useHistory } from "react-router-dom";


const Login = () => {
  const history = useHistory();
  const [validated, setValidated] = useState(false);
    const { forgot } = useSelector(s=>s.auth);
    const [error, setError] = useState({
      password: "password is required",
      cPassword: "password is required",
      otp: "otp is required",
      matchCpassword: false,
    });
    const dispatch = useDispatch();
    const [isUpdate, setIsUpdate] = useState({ updated: false, message: "" });
    const [formData, setFormData] = useState({ otp: "", password: "",cPassword:'' });

     const handleSubmit = (event) => {
        event.preventDefault();
        setValidated(true);
        
       if(formData.password !== formData.cPassword){
         return setError({
           ...error,
           matchCpassword: true,
           cPassword: " Please provide a match password with confirm password.",
         });
       }else{
         setError({
           ...error,
           matchCpassword: true,
           cPassword: !formData.password ? 'confirm password is required' : "",
         });
       }
       

         if(formData.otp){
            (async () => {
              try {
                const { data } = await forgotVerifyApi({
                  ...forgot,
                  otp: formData.otp,
                  password: formData.password,
                });
                if (data) {
                  setIsUpdate(data);
                }
              } catch (err) {
                setIsUpdate({updated: true, message:'something went Wrong'})
                console.log(err);
              }
            })();

         }
     };

    const onSubmitHandler = async () =>{
       
    }
    useEffect(()=>{
        return ()=>{
            dispatch(setForgot(null));
        }   
    },[]);

    const onloginClickHandeler = () =>{
      history.push('/login');
    } 

    return (
      <div className={`${styles.loginBody} p-2 min-vh-100  `}>
        <div className={styles.bgImg}>
          <Container>
            <Row>
              <Col md={1}></Col>
              <Col md={10} className={styles.bgWhite}>
                <Row className="m-0">
                  {!isUpdate.updated ? (
                    <Col className="p-0" md={6}>
                      <Form
                        noValidate
                        validated={validated}
                        onSubmit={handleSubmit}
                      >
                        <div className={`${styles.blueBg} text-white`}>
                          <div>
                            <h2 className={`${styles.heading}`}>
                              Reset Account
                            </h2>
                          </div>
                          <div>
                            <h5>Don't worry... it happens to the best of us</h5>
                            <div className={styles.lineWhite}></div>
                          </div>
                          {isUpdate.message && (
                            <div className={`${styles.alertD} mt-1`}>
                              <p className={`${styles.alertP} text-center`}>
                                {isUpdate.message}
                              </p>
                            </div>
                          )}
                          <div className={styles.formSection}>
                            <Form.Group
                              className="mb-3"
                              controlId="formBasicOtp"
                            >
                              <Form.Label className="text-white">
                                Enter Otp which has been sent to Email
                              </Form.Label>
                              <Form.Control
                                type="text"
                                required
                                placeholder="OTP"
                                onChange={(e) => {
                                  setFormData({
                                    ...formData,
                                    otp: e.target.value,
                                  });
                                }}
                              />
                              <Form.Control.Feedback type="invalid">
                                {error.otp}
                              </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group
                              className="mb-3"
                              controlId="formBasicPassword"
                            >
                              <Form.Label className="text-white">
                                Enter your new Password
                              </Form.Label>
                              <Form.Control
                                type="password"
                                required
                                placeholder="Password"
                                onChange={(e) => {
                                  setFormData({
                                    ...formData,
                                    password: e.target.value,
                                  });
                                }}
                              />
                              <Form.Control.Feedback type="invalid">
                                {error.password}
                              </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group
                              className="mb-3"
                              controlId="formBasicPassword"
                            >
                              <Form.Label className="text-white">
                                Confirm Password
                              </Form.Label>
                              <Form.Control
                                type="password"
                                required
                                placeholder="Confirm Password"
                                isInvalid={validated && error.cPassword}
                                onChange={(e) => {
                                  setFormData({
                                    ...formData,
                                    cPassword: e.target.value,
                                  });
                                }}
                              />
                              <Form.Control.Feedback type="invalid">
                                {error.cPassword}
                              </Form.Control.Feedback>
                            </Form.Group>
                            <Row>
                              <Form.Group
                                as={Col}
                                md={6}
                                className="mb-3"
                                controlId="formBasicCheckbox"
                              ></Form.Group>
                            </Row>
                          </div>
                          <div className="text-center">
                            <Button
                              type="submit"
                              onClick={onSubmitHandler}
                              className={`${styles.btnPrimary} px-5 `}
                            >
                              submit
                            </Button>
                          </div>
                        </div>
                      </Form>
                    </Col>
                  ) : (
                    <Col md="6">
                      <div className={styles.update}>
                        <div className="text-center pt-5">
                          <Button
                            as={Link}
                            to="/login"
                            onClick={onloginClickHandeler}
                            className={`${styles.btnPrimary} px-5 `}
                          >
                            Login
                          </Button>
                        </div>
                        <div className="pt-5">
                          <p className={`${styles.updateP} text-center`}>
                            {isUpdate.message}
                          </p>
                          <p className={`${styles.updateP} text-center`}>
                            press login button for login
                          </p>
                        </div>
                      </div>
                    </Col>
                  )}
                  <Col
                    md={6}
                    className="side-img position-relative d-none d-sm-block"
                  >
                    <img
                      src="/images/Reset.png"
                      alt="Login"
                      className="img-fluid  position-absolute top-50 start-50 translate-middle"
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    );
}

export default Login
