import React, { useContext, useState } from "react";
import styles from "./Sidebar.module.css";
import { Link, withRouter } from "react-router-dom";
import {
  Col,
  Row,
  Tab,
  Nav,
  Navbar,
  Accordion,
  ListGroup,
  useAccordionButton,
} from "react-bootstrap";
import {
  FaTachometerAlt,
  FaRegCommentDots,
  FaUserEdit,
  FaCartArrowDown,
  FaRegBell,
} from "react-icons/fa";
import Modal from "../Model/Model";
import UserForm from "../UserForm/UserForm";
import { IconContext } from "react-icons";
import { useSelector, useDispatch } from "react-redux";
import { updateFormApi } from "student/http";
import { setUser } from "student/store/auth";
import { NavLink } from "react-router-dom";
import { toast } from "react-toastify";
import ImageCustom from "student/components/ImageCustom/ImageCustom";

const Sidebar = ({ routers, match }) => {
  const { user } = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const [isUnmount, setIsUnmount] = useState(false);

  const onSubmitHandler = async (v) => {
    const formData = new FormData();
    formData.append("name", v.name);
    formData.append("country", v.country);
    formData.append("password", v.password);
    formData.append("phone", v.phone);
    formData.append("image", v.image);
    try {
        const tId = toast.loading("Please wait...");
       await updateFormApi(user._id, formData)
        .then((res) => {
          if (!isUnmount) {
            console.log(res.data.data)
            dispatch(setUser(res.data.data));
            toast.update(tId, {
              render: res.data.message,
              type: "success",
              isLoading: false,
              closeButton: true,
              autoClose: 5000,
            });
          }
        })
        .catch(function (error) {
          toast.update(tId, {
            render: error.response.data?.message,
            type: "error",
            isLoading: false,
            closeButton: true,
            autoClose: 5000,
          });
        });
    } catch (error) {
      console.log(error);
    }
  };
   
  function ContextAwareToggle({ e, eventKey }) {
    const decoratedOnClick = useAccordionButton(eventKey);

    return (
      <Nav.Item>
        <Nav.Link
          as={NavLink}
          onClick={decoratedOnClick}
          activeClassName={styles.navButtonActive}
          className={`${styles.navButton} d-flex`}
          to={`${match.path}${e.path}`}
        >
          <div style={{ padding: 10, margin: 0 }}>{e.icon}</div>
          <span>{e.name}</span>
        </Nav.Link>
      </Nav.Item>
    );
  }

  return (
    <div className="">
      <div className={`${styles.orangeBgNew} d-none d-md-block `}>
        <div>
          <Col md={2}></Col>
          <div className={styles.orengeCircle}>
            <ImageCustom
              textStyle={{fontSize: 50}}
              className={`${styles.orengeCircleImg} img-fluid`}
              src={user?.image}
              alt={user?.name||'I'}
            />
          </div>
          <div className={`${styles.blueBgNew} d-none d-md-block`}>
            <p className={styles.blueBgNewP}>
              {user?.name || "User Name"}
              <span className={`${styles.blueBgIcon} float-end`}>
                <Modal
                  buttonText={<FaUserEdit />}
                  className={styles.blueBgIconButton}
                >
                  <UserForm user={user} onSubmit={onSubmitHandler} />
                </Modal>
              </span>
            </p>
          </div>
          <div className="align-item-start">
            <Nav className="flex-md-column flex-sm-row justify-content-sm-center">
              <IconContext.Provider
                value={{ color: "#fff", size: 20, padding: 10 }}
              >
                {routers.map((e, i) => {
                  if (e.children) {
                    return (
                      <Accordion key={i} flush>
                        <ContextAwareToggle e={e} eventKey={i} />
                        <Accordion.Collapse eventKey={i}>
                          <ListGroup as="ol" numbered>
                            {e.children.map((c, j) => (
                              <NavLink
                                key={j}
                                as="li"
                                activeClassName={styles.navChildButtonActive}
                                className={`${styles.navChildButton} d-flex`}
                                to={`${match.path}${e.path}${c.path}`}
                              >
                                {c.name}
                              </NavLink>
                            ))}
                          </ListGroup>
                        </Accordion.Collapse>
                      </Accordion>
                    );
                  }
                  if (!e.name) {
                    return;
                  }
                  return (
                    <Nav.Item key={i}>
                      <Nav.Link
                        as={NavLink}
                        activeClassName={styles.navButtonActive}
                        className={`${styles.navButton} d-flex`}
                        to={`${match.path}${e.path}`}
                      >
                        <div style={{ padding: 10, margin: 0 }}>{e.icon}</div>
                        <span>{e.name}</span>
                      </Nav.Link>
                    </Nav.Item>
                  );
                })}
              </IconContext.Provider>
            </Nav>
          </div>
        </div>
      </div>
      <Navbar
        fixed="bottom"
        bg="dark"
        className={`${styles.bottomNavbar} d-md-none `}
      >
        <IconContext.Provider value={{ color: "#fff", size: 20 }}>
          <Nav
            variant="pill"
            className=" d-flex flex-row justify-content-around w-100"
          >
            {routers.map((e, i) => {
              if (!e.name) {
                return;
              }
              return (
                <Nav.Item key={i}>
                  <Nav.Link
                    as={Link}
                    className={`${styles.navButtonXs} ${
                      location.pathname === match.path + e.path
                        ? styles.navButtonActiveXs
                        : ""
                    }`}
                    to={`${match.path}${e.link}`}
                  >
                    {e.icon}
                    <br />
                    <span>{e.name}</span>
                  </Nav.Link>
                </Nav.Item>
              );
            })}
          </Nav>
        </IconContext.Provider>
      </Navbar>
    </div>
  );
};

export default withRouter(Sidebar);
