import React, { useState, useEffect } from 'react'
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import "react-perfect-scrollbar/dist/css/styles.css";
import "react-toastify/dist/ReactToastify.css";
import Navbar from 'student/components/Navbar/Navbar';
import styles from './App.module.css';
import Layout from './pages/dashboard';
import Forgot from 'student/pages/forgot/Forgot'
import Login from 'student/pages/login/Login'
import Reset from 'student/pages/reset/Reset';
import Admin from 'student/admin/Admin';
import { Provider } from 'react-redux';
import store from 'student/store';
import { useSelector } from 'react-redux';
import { ToastContainer } from "react-toastify";

import clientRouers from 'student/clientRoutes'


const App = () => {
    return (
      <Provider store={store}>
        <BrowserRouter basename="/student">
          <ToastContainer
            position="top-right"
            theme="dark"
            closeButton={true}
            autoClose={5000}
            hideProgressBar={true}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
          />
          <Switch>
            <Route path="/admin">
              <Admin />
            </Route>
            <PublicRouts path="/login">
              <Login />
            </PublicRouts>
            <PublicRouts path="/forgot">
              <Forgot />
            </PublicRouts>
            <ForgotRouts path="/reset">
              <Reset />
            </ForgotRouts>
            <PrivateRouet path="/dashbord">
              <Layout routers={clientRouers} />
            </PrivateRouet>
            <Route exact path="/">
              <Redirect to="/dashbord" />
            </Route>
          </Switch>
        </BrowserRouter>
      </Provider>
    );
}




const PrivateRouet = ({ children, ...rest }) => {
  const { isAuth, isAdminAuth } = useSelector((state) => state.auth);
  return (
    <Route
      {...rest}
      render={({ location }) => {
        return isAuth && !isAdminAuth ? (
          children
        ) : isAdminAuth ? (
          <Redirect
            to={{
              pathname: "/admin",
              state: { from: location },
            }}
          />
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
};
const PublicRouts = ({ children, ...rest }) => {
    const { isAuth } = useSelector(state => state.auth);

    return (
        <Route
            {...rest}
            render={({ location }) => {
                return (!isAuth ? children :
                    <Redirect to={{
                        pathname: '/dashbord',
                        state: { from: location }
                    }}
                    />
                )
            }

            }

        />
    )

}
const ForgotRouts = ({ children, ...rest }) => {
  const { isAuth, forgot } = useSelector((state) => state.auth);

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return !isAuth && forgot ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
};

export default App
