import React, { useState } from "react";
import {
  Col,
  Row,
  Button,
  Collapse,
  Card,
  Form,
  Tab,
  Nav,
  Container,
} from "react-bootstrap";

import { FaSearch, FaSyncAlt, FaPaste, FaTachometerAlt } from "react-icons/fa";
import { IconContext } from "react-icons";
import styles from "./DashBoard.module.css";
import TableBoot from "student/components/Table/TableBoot";
import UserTable from "student/components/Table/UserTable/UserTable";
import Search from "student/components/Search/Search";

import {
  Link,
  useRouteMatch,
  Switch,
  Route,
  NavLink,
  withRouter,
} from "react-router-dom";
;

const dRouters = [
  {
    name: "Users",
    path: "/",
    icon: <FaPaste />,
    Component: UserTable,
  },
  {
    name: "Paid And Vip Order",
    path: "/paid-vip-order",
    icon: <FaTachometerAlt />,
    Component: UserTable,
  },
  {
    name: "Revision Order",
    path: "/revision-order",
    icon: <FaTachometerAlt />,
    Component: UserTable,
  },
  {
    name: "Complete Undeliver Order",
    path: "/complete-undeliver-order",
    icon: <FaTachometerAlt />,
    Component: UserTable,
  },
  {
    name: "Complete Deliverd Order",
    path: "/complete-deliverd-order",
    icon: <FaTachometerAlt />,
    Component: UserTable,
  },
];

const DashBoard = ({ location, match }) => {

  

  return (
    <div className={`${styles.bgLightWhite} position-relative`}>
      <Tab.Container id="left-tabs-dashboard-example">
        <div className={`${styles.darkBlue} rounded-top p-2 `}>
          <Search />
          <Row className="d-md-block d-none">
            <Container>
              <IconContext.Provider value={{ size: 30 }}>
                <Nav
                  variant="pills"
                  className={`${styles.tabNav} d-flex mb-3 `}
                >
                  {dRouters.map((e, i) => (
                    <Nav.Item key={i}>
                      <Nav.Link
                        as={NavLink}
                        to={`${match.path}${e.path}`}
                        className={`${styles.tabBtn}`}
                        activeClassName={styles.tabBtnActive}
                      >
                        {e.icon}
                        {e.name}
                      </Nav.Link>
                    </Nav.Item>
                  ))}
                </Nav>
              </IconContext.Provider>
            </Container>
          </Row>
        </div>
        <div className="tab-content mt-4">
          <Tab.Content>
            <Switch>
              {dRouters.map((E, i) => {
                
                return (
                  <Route key={i} exact path={`${match.path}${E.path}`}>
                  <E.Component
                  />
                </Route>
                ) 
              })}
            </Switch>
          </Tab.Content>
        </div>
      </Tab.Container>
    </div>
  );
};

export default withRouter(DashBoard);
