import React from "react";
import { useHistory } from "react-router-dom";
import { Badge, Button, ListGroup, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";

import { setOrder } from "student/store/order";
import { setReceiver } from "student/store/order";
import PerfectScrollbar from "react-perfect-scrollbar";
import { rmNotification } from "student/store/notification";

const ListGroutOrder = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const { notifications } = useSelector((s) => s.notification);
  const { user } = useSelector((s) => s.auth);
  const ps = React.useRef();

  const listOnClickHandler = (e, t) => {
    // dispatch(setSeenMessage(e._id));
    
    switch (t) {
      case "order":
        const prose = new Promise((rsv) => {
          dispatch(rmNotification(e));
          dispatch(setOrder(e.info));
          rsv();
        });
        prose.then(()=>{
          if (user.role === "user") {
            history.push("/dashbord/inner-order");
          } else {
            dispatch(setReceiver(e.info.userId));
            history.push("/admin/dashboard/inner-order");
          }
        })
        break;
      case "payment":
        break;

      default:
        break;
    }
  };
  return (
    notifications.length > 0 && (
      <ListGroup as="ol" className="rounded" numbered>
        <PerfectScrollbar
          style={{ height: "60vh" }}
          containerRef={(el) => (ps.current = el)}
        >
          {notifications.map((e) => (
            <ListGroup.Item
              key={e._id}
              action
              onClick={() => listOnClickHandler(e, e.type)}
              className="d-flex flex-column "
            >
              <div className=" w-100 d-flex flex-row justify-content-between">
                <div className="me-5">
                  <div className="fw-bold">{e.notification}</div>
                  {user.role === 'user' ? '' : `from ${e.info.country}`}
                </div>
              </div>
              <div>
                <p>
                  Translation Id:
                  <br />
                  {e.info.translationId}
                </p>
              </div>
            </ListGroup.Item>
          ))}
        </PerfectScrollbar>
      </ListGroup>
    )
  );
};

export default ListGroutOrder;
