import React, { useState, useRef } from "react";
import styles from "./InnerOrder.module.css";
import {FaFileDownload} from 'react-icons/fa'
import {
  Row,
  Col,
  Accordion,
  Button,
  Card,
  useAccordionButton,
} from "react-bootstrap";
import Chat from "student/components/Chat/Chat";
import { useSelector } from "react-redux";
import moment from "moment";
import { Redirect } from "react-router-dom";

const InnerOrder = () => {
  const { order } = useSelector((s) => s.order);
  const chatRef = useRef(null);


  function CustomToggle({ children, eventKey }) {
    const decoratedOnClick = useAccordionButton(eventKey);
    return (
    
        <Card.Header onClick={decoratedOnClick}>{children}</Card.Header>
    );
  }

  const chatHandler = () => {
    console.log('this is ref',chatRef.current)
    chatRef.current.scrollIntoView({ behavior: "smooth" });

  };

  if (!order._id) return <Redirect to="/" />;

  return (
    <div className={styles.bgLightWhite}>
      <div className={`${styles.bgSky} rounded-top`}>
        <div className="p-2 text-center">
          <h5>
            Order Id:{" "}
            <span className="text-danger fw-bold ">{order.translationId}</span>
          </h5>
        </div>
        <div className="p-2 bg-white rounded-bottom">
          <div className="p-2">
            <Row>
              <Col md="8">
                <Accordion>
                  <Accordion.Item eventKey="0" >
                    <Accordion.Header className={`${styles.accHeader}`}>
                      User Details
                    </Accordion.Header>

                    <Accordion.Collapse eventKey="0">
                      <Row className="p-3">
                        <Col md={12} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Name :
                              <span className="text-dark">
                                {" "}
                                {` ${order.userId?.name}`}
                              </span>
                            </p>
                          </div>
                        </Col>
                        <Col md={12} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Email :
                              <span className="text-dark">
                                {` ${order.userId?.email}`}
                              </span>
                            </p>
                          </div>
                        </Col>
                        <Col md={12} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Phone :
                              <span className="text-dark">{` ${order.phone}`}</span>
                            </p>
                          </div>
                        </Col>
                      </Row>
                    </Accordion.Collapse>
                  </Accordion.Item>
                  <Accordion.Item className="p-0 m-0" eventKey="1">
                    <Accordion.Header className={`${styles.accHeader}`}>
                      Order Details
                    </Accordion.Header>
                    <Accordion.Body className="p-0 m-0">
                      <Row className="p-3">
                        <Col md={6} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Order Deadline :
                              <span className="text-dark">
                                {` ${
                                  order.deadline
                                    ? moment(order.deadline).format(
                                        "DD/MM/YYYY"
                                      )
                                    : ""
                                }`}
                              </span>
                            </p>
                          </div>
                        </Col>
                        <Col md={6} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Country :
                              <span className="text-dark">{` ${order.country}`}</span>
                            </p>
                          </div>
                        </Col>
                        <Col md={6} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Source Language :
                              <span className="text-dark">
                                {` ${order.sourceLanguage}`}
                              </span>
                            </p>
                          </div>
                        </Col>
                        <Col md={6} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              PaperLength :
                              <span className="text-dark">{` ${order.your_words}`}</span>
                            </p>
                          </div>
                        </Col>
                        <Col md={6} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Target Language :
                              <span className="text-dark">
                                {` ${order.targetlanguage}`}
                              </span>
                            </p>
                          </div>
                        </Col>
                        <Col md={6} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Notarization :
                              <span className="text-dark">{` ${order.notarization}`}</span>
                            </p>
                          </div>
                        </Col>
                        <Col md={6} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Certification :
                              <span className="text-dark">
                                {` ${order.certification}`}
                              </span>
                            </p>
                          </div>
                        </Col>
                        <Col md={6} className="p-0">
                          <div
                            className={`${styles.orderText} ${styles.bgSky} rounded`}
                          >
                            <p className={styles.filedParagraph}>
                              Service :
                              <span className="text-dark">{` ${order.service_req}`}</span>
                            </p>
                          </div>
                        </Col>
                      </Row>
                    </Accordion.Body>
                  </Accordion.Item>
                </Accordion>
              </Col>
              <Col md="4">
                <div className={`${styles.bgSky}  rounded`}>
                  <div className="p-1 m-0 ">
                    <p className="text-center m-0">
                      <strong>Questions:-</strong>
                    </p>
                    <span className="m-0 p-1">
                      <strong>Q.</strong>
                      {order.message}
                    </span>
                  </div>
                </div>
                <div className={` ${styles.bgSky} mt-2 rounded`}>
                  <div className="p-1">
                    <p className="pl-1 m-0">
                      <strong>Related Links :-</strong>
                    </p>
                    <ol>
                      <span className="text-dark" style={{ fontSize: 15 }}>
                        {order?.files?.map((e) => (
                          <li key={e}>
                            <a href={e} target="_blank">
                              <FaFileDownload style={{ fontSize: 15 }} />
                              {e.substr(e.lastIndexOf("-") + 1)}
                            </a>
                          </li>
                        ))}
                      </span>
                    </ol>
                  </div>
                </div>
              </Col>
            </Row>
            <Row>
              <div className="text-center mt-3">
                <Button
                  variant="secondary"
                  onClick={chatHandler}
                  className="w-25"
                >
                  Chat
                </Button>
              </div>
            </Row>
          </div>
        </div>
      </div>
      <div className="mt-3">
        <div ref={chatRef} className="pr-5">
          <Chat />
        </div>
      </div>
    </div>
  );
};

export default InnerOrder;
