import React, { useState, useEffect } from "react";
import {
  Row,
  Col,
  Form,
  Button,
  InputGroup,
  Nav,
  Tab,
  ListGroup,
  Badge,
} from "react-bootstrap";
import { FaSearch, FaFolderPlus, FaPaperPlane } from "react-icons/fa";
import styles from "./ChatSupport.module.css";
import { IconContext } from "react-icons";
import Chat from "student/components/Chat/Chat";
import { useSelector, useDispatch } from "react-redux";
import {
  
  getChatOrderApi,
  getUserChatOrderApi,
} from "student/http";
import { setOrder } from "student/store/order";
import { setReceiver } from "student/store/order";
import PerfectScrollbar from "react-perfect-scrollbar";
import { rmOByM } from "student/store/order";


const ChatSupport = () => {
  const { user } = useSelector((state) => state.auth);
  const { order, orderByMessage } = useSelector((state) => state.order);

  const dispatch = useDispatch();
  const [searchData, setSearchData] = useState([]);
  const [searchText, setSearchText] = useState("");

  const ps = React.useRef();
  const pp = React.useRef();

  useEffect(() => {
    if (searchText) {
      const api =
        user.role === "user"
          ? getUserChatOrderApi(user._id, `translation=${searchText}`)
          : getChatOrderApi(user._id, `translation=${searchText}`);
      try {
        api
          .then((res) => {
            setSearchData(res.data.data);
            console.log(res.data);
          })
          .catch((err) => {
            console.log(err);
          });
      } catch (e) {
        console.log(e);
      }
    }
    return () => {
      setSearchData([]);
    };
  }, [searchText]);

  useEffect(() => {
    return () =>
      dispatch(
        setOrder({
          _id: "",
          name: "",
          email: "",
          paperType: "",
          paperLength: "",
          education: "",
          referencing: "",
          message: "",
          question: "",
          deadline: "",
          country: "",
          files: [],
        })
      );
  }, []);

  const navClickHandler = (v) => {
    dispatch(setOrder(v));
    // dispatch(rmOByM(v));
    if (user.role !== "user") {
      dispatch(setReceiver({ _id: v.userId }));
    }
  };

  return (
    <div className={styles.bgLightWhite}>
      <Tab.Container id="left-tabs-example" defaultActiveKey="first">
        <Row>
          <Col md={5}>
            <div className={`${styles.darkBlue} rounded p-2`}>
              <Row>
                <Col md={12}>
                  <h4 className={styles.chat}>Chat</h4>
                </Col>

                <Form.Group
                  as={Col}
                  md="12"
                  controlId="validationCustomUsername"
                >
                  <InputGroup hasValidation>
                    <Form.Control
                      type="text"
                      placeholder="Search"
                      aria-describedby="inputGroupPrepend"
                      value={searchText}
                      onChange={(e) => setSearchText(e.target.value)}
                      required
                    />
                    <Button variant="secondary">
                      <FaSearch />
                    </Button>
                  </InputGroup>

                  {searchData.length ? (
                    <PerfectScrollbar
                      containerRef={(el) => (pp.current = el)}
                      style={{ height: "50vh" }}
                    >
                      <ListGroup className="p-1">
                        {searchData.map((e) => (
                          <ListGroup.Item
                            onClick={() => {
                              navClickHandler(e), setSearchText("");
                            }}
                            key={e._id}
                          >
                            {e.translationId}
                          </ListGroup.Item>
                        ))}
                      </ListGroup>
                    </PerfectScrollbar>
                  ) : (
                    ""
                  )}
                </Form.Group>
              </Row>
              <Nav variant="pills" className="flex-column pt-2">
                <div style={{ height: "65vh" }}>
                  <PerfectScrollbar containerRef={(el) => (ps.current = el)}>
                    {orderByMessage.map((e) => (
                      <Nav.Item key={e._id}>
                        <Nav.Link
                          onClick={() => navClickHandler(e)}
                          className={`${styles.navList} ${
                            e._id === order?._id ? styles.navListActive : ""
                          } `}
                          eventKey={`first`}
                        >
                          Translation Id: {`${e.translationId}`}
                          {e.unseenMessages.length ? (
                            <Badge
                              className={styles.navListBadge}
                              pill
                              bg="danger"
                            >
                              {e.unseenMessages.length}
                            </Badge>
                          ) : (
                            ""
                          )}
                        </Nav.Link>
                      </Nav.Item>
                    ))}
                  </PerfectScrollbar>
                </div>
              </Nav>
            </div>
          </Col>
          <Col md={7}>
            <Tab.Content>
              <Tab.Pane eventKey="first">
                <Chat />
              </Tab.Pane>
            </Tab.Content>
          </Col>
        </Row>
      </Tab.Container>
    </div>
  );
};

export default ChatSupport;
