import React, { useState, useEffect } from 'react'
import { Modal, Button, Container, Row, Col, Form, } from 'react-bootstrap'
import { FaSearch, FaFolderPlus, FaPaperPlane, FaTimesCircle } from 'react-icons/fa'
import { IconContext } from 'react-icons';
import { socket } from 'student/http';
import styles from './SelectFileModal.module.css'

const SelectFileModal = (props) => {
    const [textValue, setTextValue] = useState('');
    const { user, order, receiver, files } = props.data;
    useEffect(() => {
        return () => {
            setTextValue('');
        }
    }, []);

    const sendClickHandler = () => {
        if (!order._id) return
        const reader = new FileReader();
        reader.readAsDataURL(files[0]);
        reader.onloadend =()=>{
        const chatData = {
          text: textValue,
          sender: user,
          _id: user._id,
          receiver: receiver._id,
          orderId: order?._id,
          order,
          file: reader.result,
        };
        socket.emit('chat', chatData);
    }
        setTextValue('');
        props.onHide();
    }
    const onKeyEnterHandler = (e) => {
        if (e.key === 'Enter' && !e.shiftKey) {
            sendClickHandler();
        }
    }
    return (
        <div>
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Selected Files
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h4>Order Id: {order?.translationId}</h4>
                    <br/>
                    <h6>Files:</h6>
                    {
                        files.map(e=>(
                            <p key={e.name}>
                            {e.name}
                        </p>
                        ))
                    }
                   
                </Modal.Body>
                <Modal.Footer className={styles.darkOrenge}>
                    <IconContext.Provider value={{ color: "#fff", size: 30 }}>
                        <Container>
                            <Row>
                                <Col md={1} sm={1} className='p-0 mt-2'>
                                    <Button className={`${styles.btnSend}`} onClick={props.onHide} variant='link'>
                                        <FaTimesCircle />
                                    </Button>
                                </Col>
                                <Col md={10} sm={10} className='py-1 mr-0' >
                                    <Form >
                                        <Form.Group className="" controlId="exampleForm.ControlTextarea1">
                                            <Form.Control as="textarea" rows={1} onKeyUp={onKeyEnterHandler} onChange={(e) => setTextValue(e.target.value)} value={textValue} placeholder='Type Somthing' />
                                        </Form.Group>
                                    </Form>
                                </Col>
                                <Col md={1} sm={1} className=' position-relative' >
                                    <Button className={`${styles.btnSend} mt-1 position-absolute`} onClick={sendClickHandler} variant='link'>
                                        <IconContext.Provider value={{ color: "#fff", size: 25 }}>
                                            <FaPaperPlane />
                                        </IconContext.Provider>
                                    </Button>
                                </Col>
                            </Row>
                        </Container>
                    </IconContext.Provider>
                </Modal.Footer>
            </Modal>
        </div>
    );
}
export default SelectFileModal
