import React,{useState, useEffect} from 'react'
import { Link } from 'react-router-dom'
import { Navbar,Spinner, Container, Row, Col, NavDropdown, Nav, Badge, Button, OverlayTrigger, Popover } from 'react-bootstrap'
import {
  FaBell,
  FaWallet,
  FaEnvelope,
  FaAngleDown,
  FaUserEdit,
} from "react-icons/fa";
import UserForm from '../UserForm/UserForm';
import { setUser } from "student/store/auth";
import { useDispatch, useSelector } from 'react-redux';
import { setLogOut } from 'student/store/auth';
import Modal from '../Model/Model'
import { updateFormApi } from "student/http";
import { socket } from 'student/http';
import LoadingBadge from 'student/components/LoadingBadge/LoadingBadge';

import ListGroupCustom from 'student/components/ListGroup/ListGroup';
import ListGroutOrder from 'student/components/ListGroup/ListGroupOrder';

import styles from './Navbar.module.css'

const NavbarBoot = () => {
    const { user } = useSelector(s => s.auth);
    const { notifications } = useSelector(s => s.notification);
    const { orderByMessage } = useSelector((s) => s.order);
    const dispatch = useDispatch()
     const onSubmitHandler = async (v) => {
       const formData = new FormData();
       formData.append("name", v.name);
       formData.append("country", v.country);
       formData.append("password", v.password);
       formData.append("phone", v.phone);
       formData.append("image", v.image);
       try {
         const { data } = await updateFormApi(user._id, formData);
         console.log(data);
         if (data) {
           dispatch(setUser(data.data));
         }
       } catch (error) {
         console.log(error);
       }
     };

    const logoutHandler = () => {
      socket.emit("forceDisconnect", user._id);
        dispatch(setLogOut());
    }

    return (
      <section>
        <Navbar
          fixed="top"
          bg="light"
          className={`${styles.navBar} p-0`}
          variant="light"
          expand="md"
        >
          <Container>
            <Navbar.Brand href="#home" className="text-white">
              <div style={{ width: 200 }}>
                <img
                  className="img-fluid"
                  src="/images/singapore-translators-logo.png.webp"
                  alt="student Assignment Help"
                />
              </div>
            </Navbar.Brand>

            <Navbar.Toggle
              aria-controls="basic-navbar-nav"
              className="text-white"
            >
              <FaAngleDown />
              <img
                className={`${styles.userImg} img-fluid`}
                src={user?.image}
                alt="user"
              />
            </Navbar.Toggle>
            <Navbar.Collapse
              id="basic-navbar-nav "
              className="justify-content-end"
            >
              <Nav className="">
                <Nav.Link className="d-none d-md-block" href="#order">
                  <OverlayTrigger
                    as="div"
                    trigger="click"
                    placement={"bottom"}
                    overlay={
                      <Popover as="div" id={`popover-positioned-`}>
                        <ListGroutOrder />
                      </Popover>
                    }
                  >
                    <div variant="link" className={styles.socialIconStyle}>
                      <FaBell />
                      <LoadingBadge>{notifications.length}</LoadingBadge>
                    </div>
                  </OverlayTrigger>
                </Nav.Link>
                <Nav.Link className="d-none d-md-block" href="#link">
                  <div className={styles.socialIconStyle}>
                    <FaWallet />
                    <LoadingBadge>3</LoadingBadge>
                  </div>
                </Nav.Link>
                <Nav.Link className="d-none d-md-block" href="#link">
                  <OverlayTrigger
                    as="div"
                    trigger="click"
                    placement={"bottom"}
                    overlay={
                      <Popover as="div" id={`popover-positioned-`}>
                        <ListGroupCustom />
                      </Popover>
                    }
                  >
                    <div variant="link" className={styles.socialIconStyle}>
                      <FaEnvelope />
                      <LoadingBadge>
                        {orderByMessage.reduce(
                          (p, c) => p + c.unseenMessages.length,
                          0
                        )}
                      </LoadingBadge>
                    </div>
                  </OverlayTrigger>
                </Nav.Link>
                <Nav.Link className=" text-end d-md-none" href="#edit-profile">
                  <Modal
                    buttonText={<div>Edit Profile {<FaUserEdit />}</div>}
                    className={styles.logout}
                  >
                    <UserForm user={user} onSubmit={onSubmitHandler} />
                  </Modal>
                </Nav.Link>
                <Nav.Link className="text-end" href="#">
                  <button onClick={logoutHandler} className={styles.logout}>
                    Logout
                  </button>
                </Nav.Link>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
      </section>
    );
}

export default NavbarBoot
